import { Flex,Box,Text } from "@chakra-ui/layout"
import { Dropdown } from "../../UI/DropdownChak"
import {useState} from 'react'
import { GraphSelector } from "./GraphSelector";
import { CalcDataFunc } from "../../Calculations/calculation";

export const GraphArea2 = ({
    graphWidth,
    VKToption,
    dataRaw,
    cellArea_ratio,
    caseOption,
    PV_performanceRatio,
    PV_CellNominalEffiency,
    text,
    }) => { 
    let attributes = new Array();
    let desc = new Array();
    for (var props in text){
        if (text[props].title) {
        attributes.push(text[props].title)
        desc.push(text[props].desc)}
    }
    const  dataOriginal = CalcDataFunc({
        data: dataRaw,
        VKToption: VKToption,
        PV_CellNominalEffiency: PV_CellNominalEffiency/100,
        cellArea_ratio: cellArea_ratio/100,
        caseOption:caseOption,
        PV_performanceRatio:PV_performanceRatio/100,
        useStat: false,
        })
    
    const [graph, setGraph] = useState("EV vs PV-on-EV Emissions over time")
    return (
    <Flex 
        borderWidth="0px" 
        direction="column" 
        width="100%"
        height="95%"
        alignItems="center" 
        justifyContent="center"
        background="#fffff">
        <Flex mt="2%" width="100%">
        <Dropdown
            label = {"Select Graph:"}
            options={attributes}
            id = {'menuDrop'}
            selectedValue={graph}
            onSelectedValueChange={setGraph}
            width={"100%"}
            tooltip = {"Here you can select different graphs to look at."}
        />
        </Flex>
        <Text paddingInline="2%" marginBlock="2%" marginInline="8%" background="white">
        {desc[attributes.indexOf(graph)]}
        </Text>
        <Box id="box" flex="1" height="90%" width="95%" marginInline="1%">
            <GraphSelector
                dataOriginal={dataOriginal}
                graph={graph}
                graphWidth = {graphWidth}
                attributes = {attributes}
            />
        </Box>
    </Flex>
)
    };