export const OuterRegion = ({data,innerHeight,xScale,yScale,graphWidth}) => (
    <>
                <text
                    fontWeight="800"
                    textAnchor="middle"
                    transform={`translate(${xScale(5.52*2/3)},${innerHeight * 7/8})`}
                >
                    {"Unreasonable"}
                </text>
                <text
                    fontWeight="700"
                    textAnchor="middle"
                    transform={`translate(${xScale(5.52*2/3)},${innerHeight * 15/16})`}
                    fontSize={graphWidth>525 ?"16px":"11px"}
                >
                    {"(PV-efficiency too low)"}
                </text>
                <polygon 
                stroke="none"
                points={`${xScale(0)},${yScale(0)} ${xScale(5.52)},${yScale(data[0].Wp)} ${xScale(5.52)},${yScale(0)}` }
                fill="red"
                opacity="0.2"
                />
                <text
                    fontWeight="800"
                    textAnchor="middle"
                    transform={`translate(${xScale(5.52/3)},${innerHeight/3})`}
                >
                    {"Unrealistic"}
                </text>
                <text
                    fontWeight="700"
                    textAnchor="middle"
                    transform={`translate(${xScale(5.52/3)},${innerHeight* 7/18})`}
                    fontSize={graphWidth>525 ?"16px":"11px"}
                >
                    {"(PV-efficiency too high)"}
                </text>
                <polygon 
                stroke="none"
                points={`${xScale(0)},${yScale(0)} ${xScale(0)},${yScale(data[4].Wp)} ${xScale(5.52)},${yScale(data[4].Wp)}` }
                fill="red"
                opacity="0.2"
                />
    </>
)