export const AxisLeft = ({yScale, innerWidth}) => 

    yScale.ticks().map(tickValue => ( 

        <g 
            className = "tick"
            key={tickValue} 
            transform={`translate(0,${yScale(tickValue)})`}
        >

        <line 
            x2= {innerWidth} 
            stroke="black"
        />

        <text 
            x={-10} 
            style={{textAnchor:"end" }} 
            dy="0.32em"> 
            {tickValue} 
        </text> 

    </g>               
))