import {
    select,
    scaleLinear,
    axisBottom,
    axisLeft,
    max,
    scaleOrdinal,
    extent,
    line,
    format,
    selection,
} from "d3";

import React, { useRef, useEffect } from "react";
import { ColourLegend } from "./colourLegend";

selection.prototype.moveToFront = function() {
  return this.each(function(){
  this.parentNode.appendChild(this);
  });
};

export const Linechart = ({ 
    graphWidth,
    dataOriginal,  
    xAxisTitle,
    yAxisTitle,
    Title,
    xValue,
    yValue,
    xTitleOffset,
    yTitleOffset,
    titleOffset,
    colorTheme,
    radius,
    country,
    toolTipText1,
    formatNumber1,
    toolTipText2,
    formatNumber2,
    height,
    abbrev,
    legHov,
    onLegHov,
    legClick,
    onLegClick,
    chartColour,
    axisSize,
    margin,
}) => {
  const duration = 2000;
  const width= graphWidth; 
  const data = dataOriginal.filter(d => yValue(d))
  
  const innerWidth = width - margin.left - margin.right;
  const innerHeight = height - margin.top - margin.bottom;
  const ref = useRef();

  let temp3 = [];

  for(let j=0;j<data.length;j++){
    let temp2 = []
    for(let i=0;i<data[0].Year.length;i++){
        let temp = {}
        temp.Year= data[j].Year[i]
        temp.EV_Usage_Emissions_Years= data[j].EV_Usage_Emissions_Years[i]
        temp.PVonEV_usageEmissions_Years= data[j].PVonEV_usageEmissions_Years[i]
        temp.CIEG_gCO2_kWh_Years = data[j].CIEG_gCO2_kWh_Years[i]
        temp2.push(temp)
      }
    temp3.push(temp2)
    }
  const dataFormatted = temp3
  useEffect(() => {
    draw();
  }, [data]); 

  const draw = () => {

    let maxY = 0;

    data.map( d => {
    if (maxY < max(yValue(d)))
      maxY = max(yValue(d))
    })

    const xScale = scaleLinear()
      .domain(extent(data[0].Year))
      .range([0, innerWidth])
      .nice();

    const yScale = scaleLinear()
      .domain([0, maxY])
      .range([innerHeight, 0])
      .nice();

    const xAxis = axisBottom().scale(xScale).tickSizeInner(-innerHeight).tickPadding(10).tickFormat(format("d"));

    const yAxis = axisLeft().scale(yScale).tickSizeInner(-innerWidth).tickPadding(10);

    const colorScale = scaleOrdinal(colorTheme).domain(data.map((d,i) => i));

    const svg = select(ref.current);
    const chart = svg.select("g");

    const tooltip = select('.tooltip')
        .style('position', 'absolute')
        .style('z-index', '10')
        .style('visibility', 'hidden')
        .style('padding', '10px')
        .style('background', 'rgba(0,0,0,0.6)')
        .style('border-radius', '4px')
        .style('color', '#fff')
        .text('a simple tooltip');

        chart//background
        .selectAll(".background")
        .data([null])
        .join('rect')
        .classed('background',true)
        .attr('width',innerWidth)
        .attr('height',innerHeight)
        .style('fill',chartColour)
        .on('click', () => {
          onLegClick(null)
        })
  
      const xAxisGroup = chart.selectAll(".xAxisGroup").data([null]).join("g").classed("xAxisGroup",true)
      const yAxisGroup = chart.selectAll(".yAxisGroup").data([null]).join("g").classed("yAxisGroup",true)
  
      xAxisGroup//y-axis
      .selectAll(".x-axis")
      .data([null])
      .join((enter) =>
        enter
          .append("g")
          .style("font-size", "0.9em")
          .classed("x-axis", true)
          .attr("transform", `translate(0,${innerHeight})`),
        (update) =>
          update,
          (exit) => 
          exit
          .transition()
          .duration(duration)
          .remove()
      )
      .transition()
      .duration(duration)
      .call(xAxis);

  if(xAxisGroup.select('.tick text').text()===0){
  xAxisGroup.select('.tick line').remove()
  }
    

    xAxisGroup//x-axis title
      .selectAll(".x-axis-title")
      .data([xAxisTitle])
      .join("text")
      .classed("x-axis-title", true)
      .attr("x", innerWidth / 2)
      .attr("y", innerHeight + xTitleOffset)
      .style("font-size", axisSize)
      .attr("fill", "black")
      .style("text-anchor", "middle")
      .text((d) => d);

    
    yAxisGroup//y-axis
    .selectAll(".y-axis")
    .data([null])
    .join((enter) =>
      enter
        .append("g")
        .style("font-size", "0.9em")
        .classed("y-axis", true)
        .attr("transform", "translate(0,0)"),
      (update) =>
        update,
        (exit) => 
        exit
        .transition()
        .duration(duration)
        .remove()
    )
    .transition()
    .duration(duration)
    .call(yAxis);
    
    yAxisGroup//y-axis title
      .selectAll(".y-axis-title")
      .data([yAxisTitle])
      .join("text")
      .classed("y-axis-title", true)
      .attr("x", 0)
      .attr("y", yTitleOffset)
      .attr("opacity",1)
      .style("font-size", axisSize)
      .attr("transform", `translate(${yTitleOffset}, ${innerHeight / 2}) rotate(-90)`)
      .attr("fill", "#000")
      .style("text-anchor", "middle")
      .text((d) => d);

    yAxisGroup//y-axis
      .selectAll(".xydomain")
      .data([null])
      .join((enter) =>
        enter
          .append("path")
          .classed("xydomain", true),
        (update)=>
          update
          ,
          (exit) => 
          exit
          .transition()
          .duration(duration)
          .remove()
      )      
      .transition()
      .duration(duration)
      .attr('fill','none')
      .attr('d',`M0,0V${innerHeight} H${innerWidth}`)
      .attr('stroke',"black")        
  
      chart //chart title
          .selectAll(".chart-title")
          .data([Title])
          .join("text")
          .classed("chart-title",true)
          .attr("x", innerWidth / 2)
          .attr('y',titleOffset)
          .attr("fill", "#000")
          .style("font-size", "14px")
          .style("text-anchor", "middle")
          .text((d) => d);

    const lineGenerator = line()
      .x(d => xScale(xValue(d)))
      .y(d => yScale(yValue(d)))

    for(let i=0; i<dataFormatted.length;i++){

    const points = chart//points
      .selectAll(`.${data[i].abbrev}Marks`)
      .data(dataFormatted[i])
      .join((enter) =>
        enter
          .append("circle")
          .classed(`${data[i].abbrev}Marks`, true)
          .attr("cy", (d) => yScale(yValue(d)))
          .attr("cx", (d) => xScale(xValue(d)))
          .attr('r',0),
        (update) =>
          update,
          (exit) => 
          exit
          .transition()
          .duration(duration)
          .attr('r',0)
          .remove()
      )
      .style("fill", () => colorScale(i))
      .on('mouseover',  d => {
        const temp = d.target.__data__
        tooltip
          .html(
            `<div>${country(data[i])}:</div><div>${toolTipText1 + xValue(temp).toFixed(0)} ${formatNumber1}</div><div>${toolTipText2 + yValue(temp).toFixed(2)} ${formatNumber2}</div>`
          )
          .style('visibility', 'visible');
        select(this).transition().attr('fill', "#eec42d");
        })
      .on('mousemove', function (d) {
            tooltip
            .style('top', d.pageY -10 + 'px')
            .style('left', d.pageX +10 + 'px');
        })
      .on('mouseout', function () {
            tooltip.html(``).style('visibility', 'hidden');
            select(this).transition().attr('fill', '#437c90');
        })

      points
      .transition('opac')
      .duration(duration*0.2)
      .attr('opacity', () => {
        if (legClick === abbrev(data[i]))
            {return 0.9}
        else {
        if (!!legHov){
          if (legHov  === abbrev(data[i]))
            {return 0.9}
          else {return 0.2}
        }
        else if (!legClick && !legHov) {return 0.9}
        else {return 0.2}
      }}
      )

      points
      .transition('pos')
      .duration(duration)
      .attr("cy", (d) => yScale(yValue(d)))
      .attr("cx", (d) => xScale(xValue(d)))
      .attr('r', radius)


    const lines = chart//lines
      .selectAll(`.${data[i].abbrev}Line`)
      .data([null])
      .join((enter) =>
        enter
          .append("path")
          .classed(`${data[i].abbrev}Line`, true)
          .attr('opacity',0)
          .attr('fill','none'),
        (update) =>
          update,
          (exit) => 
          exit
          .transition()
          .duration(duration)
          .remove()
      )
      .style("stroke", colorScale(i))
      .style("stroke-width",2)

      lines
      .transition("appear")
      .duration(duration)
      .attr('d', d => lineGenerator(dataFormatted[i]))
      
      lines
      .transition("opac")
      .duration(duration*0.2)
      .attr('opacity', () => {
        if (legClick === abbrev(data[i]))
            {return 1}
        else {
        if (!!legHov){
          if (legHov  === abbrev(data[i]))
            {return 1}
          else {return 0.2}
        }
        else if (!legClick && !legHov) {return 1}
        else {return 0.2}
      }}
      )
    }

    ColourLegend({
      colorScale: colorScale,
      selection: chart,
      duration: duration,
      data: data,
      abbrev: abbrev,
      position: `(${innerWidth- 100},20)`,
      radius: 5,
      legHov: legHov,
      onLegHov : onLegHov,
      legClick: legClick,
      onLegClick: onLegClick,
    })
    chart.selectAll('.legend').moveToFront()
};

  return (
    <div id="linechart">
        <svg className="chart" width={width} height={height}  ref={ref}>
            <g transform={`translate(${margin.left}, ${margin.top})`}/>
        </svg>
        <div className="tooltip"/>
    </div>
  );
}