
// constant assumptions

const PV_cellarea_cm2 = 153.3;
const STCirradiance_W_m2 = 1000;
//const PV_converterEfficency = 0.93
const PV_lifetime_yrs = 25;
const PV_carbon_intensity_per_unitCapacity = 1500;
const EV_ChargerEfficiency = 0.86;
const annualElecCostIncrease = 0.025;
const a1 =	-0.0348;
const a2 =	0.9318;
const d1 =	11.826;
const d2 = 	-8.827;


export const CalcDataFunc = props => {

let {
    data,
    VKToption,
    PV_CellNominalEffiency,
    cellArea_ratio,
    caseOption,
    PV_performanceRatio,
} = props

    let pvData = data
    let VKT = new Array(pvData.length);
    let VKT_day = new Array(pvData.length);
    // Assumption Calculation
    if (!VKToption[1])
          VKT.fill(VKToption[0])
    else for (let i = 0; i <pvData.length ; i++) {VKT[i] = pvData[i].VKTstat}

    const caseArray = ['High Case','Medium Case','Low Case'];
    const energy_consumption_Wh_km  = [280,200,150]
    const vehicle_footprint_m2Array = [4.3,4.3,3.68]

    for (let i = 0; i <pvData.length ; i++) {
         VKT_day[i] = VKT[i]/365;
    }
    const evCaseSelect = caseArray.indexOf(caseOption[0])
    let EV_Eff_Wh_km;
    let vehicle_footprint_m2;

    if (caseOption[1] === false) {
     EV_Eff_Wh_km = energy_consumption_Wh_km[evCaseSelect]
     vehicle_footprint_m2 =  vehicle_footprint_m2Array[evCaseSelect]
    }
    else {
    EV_Eff_Wh_km = caseOption[2]
     vehicle_footprint_m2 =  caseOption[3]
    }
    const cellNumber = Math.floor(vehicle_footprint_m2*cellArea_ratio*10000/PV_cellarea_cm2)
    //const PV_totalArrayArea = PV_cellarea_cm2*cellNumber/10000;
    const PV_CellNominalPower = PV_CellNominalEffiency*STCirradiance_W_m2*PV_cellarea_cm2/10000;
    const PV_arrayNominalPower = PV_CellNominalPower * cellNumber;
    //const PV_arrayLosses = 1- PV_performanceRatio/PV_converterEfficency;
    
    const EV_Eff_km_kWh =  1/EV_Eff_Wh_km*1000
    let manufacturingEmissions = [];
    for (let i = 0; i <pvData.length ; i++) {manufacturingEmissions.push(PV_arrayNominalPower*PV_carbon_intensity_per_unitCapacity/(VKT[i]*PV_lifetime_yrs))};



    // ICEV-Usage Emissions of LDV Fleet
    let ICEV_Usage_Emissions = [];
    
    pvData.forEach(d => {
        let temp = (a1*d.Share_of_diesel_gasoline_vehicles/100+a2)*d.ICEV_usage_emissions_gCO2e_km + d1*d.Share_of_diesel_gasoline_vehicles/100+d2
        ICEV_Usage_Emissions.push(temp) })
    
  


    // EV-USAGE EMISSIONS		
    let EV_Usage_Emissions = []
    let temp;
    pvData.forEach(d => {
        if (typeof(d.CIEG_kgCO2_kWh)=== "number")
            temp = d.CIEG_kgCO2_kWh/EV_Eff_km_kWh*1000
        else {
            temp = new Array(d.CIEG_kgCO2_kWh.length)
            for(let i = 0; i <d.CIEG_kgCO2_kWh.length ; i++){
                temp[i] = d.CIEG_kgCO2_kWh[i]/EV_Eff_km_kWh*1000
            }
        }
        EV_Usage_Emissions.push(temp) })
        


    // GRID ENERGY SAVINGS
    let PV_output_kWh_yr =[];
    let gridSavings_kWh_yr = [];

    pvData.forEach(d => {
        let temp = d.Annual_GHI_kWh_m2*PV_performanceRatio*PV_arrayNominalPower/1000
        PV_output_kWh_yr.push(temp)
        gridSavings_kWh_yr.push(temp/EV_ChargerEfficiency)       })


    
    // PV-ONLY DRIVING RANGE	
    let PV_only_drivingRange_km_day = [];
    let PV_only_drivingRange_perc = [];

    let n=0;
    gridSavings_kWh_yr.forEach(d => {
        let temp = d*EV_Eff_km_kWh/365; 
        PV_only_drivingRange_km_day.push(temp)
        
        if (temp/VKT_day[n] >= 1)
             PV_only_drivingRange_perc.push(1);
        else PV_only_drivingRange_perc.push(temp/VKT_day[n]);
        n += 1;  
    })

    
     
    //PV ON EV-USAGE EMISSIONS						
    let emissionReduction_savings = [];
    let PVonEV_usageEmissions =[];
    let PVonEV_EV_ratio =[];
    let EV_Usage_Emissions_change = [];

    n=0;
    pvData.forEach(d => {
        if (typeof(d.CIEG_kgCO2_kWh)=== "number"){
            let temp = (gridSavings_kWh_yr[n]*d.CIEG_kgCO2_kWh*1000)/VKT[n]
            emissionReduction_savings.push(temp) 
            if (EV_Usage_Emissions[n]>temp)
                PVonEV_usageEmissions.push(EV_Usage_Emissions[n]+manufacturingEmissions[n]-temp)
            else PVonEV_usageEmissions.push(manufacturingEmissions[n])
            PVonEV_EV_ratio.push((EV_Usage_Emissions[n]-PVonEV_usageEmissions[n])/EV_Usage_Emissions[n])
            EV_Usage_Emissions_change.push(-EV_Usage_Emissions[n]+PVonEV_usageEmissions[n])
        }
        else  {
           let temp = new Array(d.CIEG_kgCO2_kWh.length)
           let temp2 = new Array(d.CIEG_kgCO2_kWh.length)
           let temp3 = new Array(d.CIEG_kgCO2_kWh.length)
           let temp4 = new Array(d.CIEG_kgCO2_kWh.length)
        for(let i = 0; i <d.CIEG_kgCO2_kWh.length ; i++){
             temp[i] = (gridSavings_kWh_yr[n]*d.CIEG_kgCO2_kWh[i]*1000)/VKT[n]
            if (EV_Usage_Emissions[n][i]>temp[i])
                temp2[i] = EV_Usage_Emissions[n][i]+manufacturingEmissions[n]-temp[i]
            else temp2[i] = manufacturingEmissions[n]
            temp3[i] = (EV_Usage_Emissions[n][i]-temp2[i])/EV_Usage_Emissions[n][i]
            temp4[i] = (-EV_Usage_Emissions[n][i]+temp2[i])
        }
            emissionReduction_savings.push(temp) 
            PVonEV_usageEmissions.push(temp2)
            PVonEV_EV_ratio.push(temp3)
            EV_Usage_Emissions_change.push(temp4)
        }
        n += 1; 
    })




    //PV ON EV EQUIVALENT CIEG REDUCTION			
    let CIEG_trends_gCO2e_kWh = [];
    let CIEG_PV_trends_gCO2e_kWh = [];
    let CIEG_PV_change_trends_gCO2e_kWh = [];
    n=0;

    pvData.forEach(d => {
        if (typeof(d.CIEG_kgCO2_kWh)=== "number"){
        let temp = d.CIEG_gCO2_kWh -d.CIEG2013_gCO2e_kWh
        let temp2 = PVonEV_usageEmissions[n]*1000/EV_Eff_Wh_km
        CIEG_trends_gCO2e_kWh.push(temp) 
        CIEG_PV_trends_gCO2e_kWh.push(temp2)
        CIEG_PV_change_trends_gCO2e_kWh.push(temp2 - d.CIEG_gCO2_kWh)
        }
        else{
            let index = d.Year.indexOf(2018)
            let temp = d.CIEG_gCO2_kWh[index] -d.CIEG2013_gCO2e_kWh
            let temp2 = PVonEV_usageEmissions[n][index]*1000/EV_Eff_Wh_km
            let temp3 = temp2 - d.CIEG_gCO2_kWh[index]
            
            CIEG_trends_gCO2e_kWh.push(temp) 
            CIEG_PV_trends_gCO2e_kWh.push(temp2)
            CIEG_PV_change_trends_gCO2e_kWh.push(temp3)
        }
        n += 1;   
    })




    //ELECTRICITY COST SAVINGS					
    let EV_Electricitycost_USD_yr = [];
    let PVonEV_Savings_USD_yr = [];
    let PVonEV_Cost_USD_yr = [];
    let PVonEV_LifetimeSavings_USD_yr = [];
    n = 0;
    pvData.forEach(d => {
        let temp = d.Electricity_cost_USD_kWh*VKT[n]*EV_Eff_Wh_km/1000

        EV_Electricitycost_USD_yr.push(temp)

        if (d.Electricity_cost_USD_kWh*gridSavings_kWh_yr[n] < temp)
            PVonEV_Savings_USD_yr.push(d.Electricity_cost_USD_kWh*gridSavings_kWh_yr[n]);
        else  PVonEV_Savings_USD_yr.push(temp)

        PVonEV_Cost_USD_yr.push(temp - d.Electricity_cost_USD_kWh*gridSavings_kWh_yr[n])
        let temp2 = PVonEV_Savings_USD_yr[n];
        for (let i = 1; i <15 ; i++) {
            temp2 += PVonEV_Savings_USD_yr[n]*Math.pow(1+annualElecCostIncrease,i)
          }
        PVonEV_LifetimeSavings_USD_yr.push(temp2)
        n+=1;
    });

    
    
    let calculatedData = pvData;
    for (let i = 0; i <pvData.length ; i++) {

        if (pvData[i].G20 === "TRUE")
            {calculatedData[i].G20 = true}

        calculatedData[i].ICEV_Usage_Emissions = ICEV_Usage_Emissions[i]


        calculatedData[i].PV_output_kWh_yr= PV_output_kWh_yr[i]
        calculatedData[i].gridSavings_kWh_yr= gridSavings_kWh_yr[i]

        calculatedData[i].PV_only_drivingRange_km_day= PV_only_drivingRange_km_day[i]
        calculatedData[i].PV_only_drivingRange_perc= PV_only_drivingRange_perc[i]

        if (typeof(EV_Usage_Emissions[i]) === "number") {
            calculatedData[i].EV_Usage_Emissions=  EV_Usage_Emissions[i]
            calculatedData[i].emissionReduction_savings = emissionReduction_savings[i] 
            calculatedData[i].PVonEV_usageEmissions =PVonEV_usageEmissions[i];
            calculatedData[i].PVonEV_EV_ratio =PVonEV_EV_ratio[i];
            calculatedData[i].EV_Usage_Emissions_change = EV_Usage_Emissions_change[i];
            calculatedData[i].CIEG_gCO2_kWh_2018 = calculatedData[i].CIEG_gCO2_kWh
            calculatedData[i].CIEG_kgCO2_kWh_2018 = calculatedData[i].CIEG_kgCO2_kWh
        }
        else {
            calculatedData[i].EV_Usage_Emissions_Years=  EV_Usage_Emissions[i]
            calculatedData[i].emissionReduction_savings_Years = emissionReduction_savings[i] 
            calculatedData[i].PVonEV_usageEmissions_Years =PVonEV_usageEmissions[i];
            calculatedData[i].PVonEV_EV_ratio_Years =PVonEV_EV_ratio[i];
            calculatedData[i].EV_Usage_Emissions_change_Years = EV_Usage_Emissions_change[i];
            calculatedData[i].CIEG_kgCO2_kWh_Years = calculatedData[i].CIEG_kgCO2_kWh 
            calculatedData[i].CIEG_gCO2_kWh_Years = calculatedData[i].CIEG_gCO2_kWh

            let index = calculatedData[i].Year.indexOf(2018)
            calculatedData[i].EV_Usage_Emissions=  EV_Usage_Emissions[i][index]
            calculatedData[i].emissionReduction_savings = emissionReduction_savings[i][index]
            calculatedData[i].PVonEV_usageEmissions =PVonEV_usageEmissions[i][index];
            calculatedData[i].PVonEV_EV_ratio =PVonEV_EV_ratio[i][index];
            calculatedData[i].EV_Usage_Emissions_change = EV_Usage_Emissions_change[i][index];
            calculatedData[i].CIEG_gCO2_kWh_2018 = calculatedData[i].CIEG_gCO2_kWh[index]
            calculatedData[i].CIEG_kgCO2_kWh_2018 = calculatedData[i].CIEG_kgCO2_kWh[index]
        }
        calculatedData[i].CIEG_trends_gCO2e_kWh = CIEG_trends_gCO2e_kWh[i];
        calculatedData[i].CIEG_PV_trends_gCO2e_kWh = CIEG_PV_trends_gCO2e_kWh[i];
        calculatedData[i].CIEG_PV_change_trends_gCO2e_kWh = CIEG_PV_change_trends_gCO2e_kWh[i];

        calculatedData[i].EV_Electricitycost_USD_yr = EV_Electricitycost_USD_yr[i];
        calculatedData[i].PVonEV_Savings_USD_yr = PVonEV_Savings_USD_yr[i];
        calculatedData[i].PVonEV_Cost_USD_yr = PVonEV_Cost_USD_yr[i];
        calculatedData[i].PVonEV_LifetimeSavings_USD_yr = PVonEV_LifetimeSavings_USD_yr[i];
        calculatedData[i].VKT_day = VKT_day[i];

    }
    return calculatedData;
};
