
import { MapOptions } from '../World/MapOptions';
import {UStates} from './uStates'
import { BsXSquareFill } from "react-icons/bs"
import {
  Flex, 
  Box, 
  Button,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
} from  "@chakra-ui/react"


const arraySame = ([a,b]) => {

  let truthCounter = 0;
  if (a == null || b == null) return false
  if ((a.length===0 && b.length!==0) || (a.length!==0 && b.length===0))
    return false
  else if (a.length===0 && b.length===0)
    return true

  for(let i=0; i<(a.length); i++) {
      for(let j=0; j<(b.length); j++) {
          if (a[i] === b[j])
              truthCounter += 1;
      }
  }
  if (truthCounter !== a.length) {
    return false}
  else return true
}

const arraySimilar = ([a,b]) => {
  if (a == null || b == null) return false
  if ((a.length===0 && b.length!== 0) || (a.length!==0 && b.length===0))
    return false
  else if (a.length ===0 && b.length===0)
    return true
  
  for(let i=0; i<(a.length); i++) {
      for(let j=0; j<(b.length); j++) {
          if (a[i] === b[j])
              return true;
      }
  }
  return false
}

export const StatesCreator = ({
  setlength,
  onClicked,
  setOnClicked,
  data,
  onClose,
  mapClicked,
  setMapClicked,
  countries,
  setCountries,
  regionsAvailable,

}) => {

    if (!data) {
      return <pre>Loading...</pre>;
    }
    const G20 = ["South Africa", "Indonesia", "Saudi Arabia", "Australia", "India", "China", "Japan", "South Korea", "Turkey", "Mexico", "United States of America", "Germany", "Russia", "Argentina", "Italy", "Europe", "United Kingdom", "Canada", "Brazil", "France"];    
    const EUR = ["Turkey","Germany" ,"Italy", "Europe", "United Kingdom","France","Russia"]
    const ASIA = ["China","India","Indonesia","Japan","South Korea","Russia"];
   

    return (
      <>
          <ModalHeader >
            Select a State: 
            <Button
              marginInline={3}
              leftIcon={<BsXSquareFill />}
              colorScheme="red" 
              onClick={() => {
                setCountries([])
                setMapClicked(true)
              }}>
                Clear Selection
            </Button>
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody height="100%" >

            <Flex  display="flex" flex-direction="column" height="100%" width="100%" >
              <Box flex="1" height="100%" width="100%">
              <div className="map-tooltip"></div>
              <svg className = "map-container" id = "map-container" height="100%" width="100%">
                <g className="shapes">
                <UStates
                  data={data}
                  countries = {countries}
                  setCountries = {setCountries}
                  mapClicked = {mapClicked}
                  setMapClicked = {setMapClicked}
                  regionsAvailable = {regionsAvailable}
                  />
                </g>
              </svg>
              </Box>
            </Flex>
            
          </ModalBody>
          <ModalFooter>
            <Button
            mr={3}
            colorScheme="teal" 
            onClick={() => {
              setlength(onClicked.length)
              setOnClicked(countries)
              onClose()
              }}>
             <a href ="#graphArea">Continue</a>
            </Button>
          </ModalFooter>
        </>
    )};