import { Flex,Box,Text } from "@chakra-ui/layout"
import { Dropdown } from "../../UI/DropdownChak"
import {useState} from 'react'
import { GraphSelector } from "./GraphSelector";
import { CalcDataFunc } from "../../Calculations/calculation";

export const GraphArea = ({
    onClicked,
    graphWidth,
    dataLength,
    VKToption,
    dataRaw,
    cellArea_ratio,
    caseOption,
    PV_performanceRatio,
    PV_CellNominalEffiency,
    text,
    }) => { 

    let attributes = new Array();
    let desc = new Array();
        
    for (var props in text){
        if (text[props].title) {
        attributes.push(text[props].title)
        desc.push(text[props].desc)}
    }

    const  dataOriginal = CalcDataFunc({
        data: dataRaw,
        VKToption: VKToption,
        PV_CellNominalEffiency: PV_CellNominalEffiency/100,
        cellArea_ratio: cellArea_ratio/100,
        caseOption:caseOption,
        PV_performanceRatio:PV_performanceRatio/100,
        useStat: false,
        })
    
    const [legHov, onLegHov] = useState(null)
    const [legClick, onLegClick] = useState(null)
    
    const [graph, setGraph] = useState(attributes[1])


    const changingGraph = (temp) => {
        onLegClick(null)
        onLegHov(null)
        setGraph(temp)
    }

    return (
    <Flex 
        className="column"
        borderWidth="0px" 
        direction="column" 
        width="100%" 
        alignItems="center" 
        justifyContent="center"
        >
        <Flex mt="2%" width="90%">
        <Dropdown
            label = {"Select Graph:"}
            options={attributes}
            id = {'menuDrop'}
            selectedValue={graph}
            onSelectedValueChange={changingGraph}
            width={"100%"}
            tooltip = {"Here you can select different graphs to look at."}
        />
        </Flex>
        <Text height="120" display={{base:"none", "500px":"content"}} paddingInline="2%" marginBlock="1%" marginInline="8%" background="white">
        {
            desc[attributes.indexOf(graph)]
        }
        </Text>
        <Box id="box" flex="1" height="auto" width="auto" >
            <GraphSelector
                onClicked ={onClicked}
                dataOriginal={dataOriginal}
                graph={graph}
                graphWidth = {graphWidth}
                dataLength = {dataLength}
                legHov = {legHov}
                onLegHov = {onLegHov}
                legClick = {legClick}
                onLegClick = {onLegClick}
                attributes = {attributes}
            />
        </Box>
    </Flex>
)
    };