import {Flex} from '@chakra-ui/react' 
import { useState } from 'react';

import { GraphArea2 } from "./GraphSegment";
import { UIMaker2 } from "../../UI/UI2";

export const SecondModel = ({
    graphWidth,
    dataRaw,
    text,
}) => {

  const [caseOption1,setCaseOption1] =  useState(['Medium Case',false,150,3])
  const [VKToption1, setVKTOption1 ] = useState([18000,false])
  const [cellArea_ratio1, setCellArea_ratio1 ] = useState(85)
  const [PV_CellNominalEfficiency1, setPV_CellNominalEfficiency1 ] = useState(25)
  const [PV_performanceRatio1, setPV_performanceRatio1 ] = useState(77.5)

return (
  <Flex className="row" height="auto"  margin="2%">
  <Flex className="column" width="40%">
    <UIMaker2

      caseOption={caseOption1}
      setCaseOption={setCaseOption1}
      VKToption={VKToption1}
      setVKTOption={setVKTOption1}
      PV_performanceRatio={PV_performanceRatio1}
      setPV_performanceRatio={setPV_performanceRatio1}
      PV_CellNominalEffiency = {PV_CellNominalEfficiency1}
      setPV_CellNominalEffiency = {setPV_CellNominalEfficiency1}
      cellArea_ratio = {cellArea_ratio1}
      setCellArea_ratio ={setCellArea_ratio1}
      graphWidth = {graphWidth}
      />
  </Flex>
  <Flex id="graphArea2" className="column" width="60%">
    <GraphArea2
      graphWidth = {graphWidth}
      dataRaw = {dataRaw}
      VKToption = {VKToption1}
      cellArea_ratio ={cellArea_ratio1}
      caseOption = {caseOption1}
      PV_performanceRatio ={PV_performanceRatio1}
      PV_CellNominalEffiency ={PV_CellNominalEfficiency1}
      text={text}
    />
  </Flex>
</Flex>
)
}