import {Flex} from '@chakra-ui/react' 
import { useState } from 'react';

import { GraphArea } from "./GraphSegment";
import { UIMaker } from "../../UI/UI";

export const MainModel = ({
    graphWidth,
    dataRaw,
    modalHeight,
    text,
}) => {

const [onClicked,setOnClicked] = useState(['United States of America'])
const [dataLength,setlength] = useState(onClicked.length)

const [caseOption,setCaseOption] = useState(['Medium Case',false,150,3])
const [VKToption, setVKTOption ] = useState([15000,false])
const [cellArea_ratio, setCellArea_ratio ] = useState(85)
const [PV_CellNominalEfficiency, setPV_CellNominalEfficiency ] = useState(25)
const [PV_performanceRatio, setPV_performanceRatio ] = useState(77.5)

return (
    <Flex className="row" height="auto" margin="2%">
        <Flex className="column" width="40%">
          <UIMaker 
            onClicked={onClicked}
            setOnClicked={setOnClicked}
            caseOption={caseOption}
            setCaseOption={setCaseOption}
            VKToption={VKToption}
            setVKTOption={setVKTOption}
            PV_performanceRatio={PV_performanceRatio}
            setPV_performanceRatio={setPV_performanceRatio}
            PV_CellNominalEffiency = {PV_CellNominalEfficiency}
            setPV_CellNominalEffiency = {setPV_CellNominalEfficiency}
            cellArea_ratio = {cellArea_ratio}
            setCellArea_ratio ={setCellArea_ratio}
            modalHeight= {modalHeight}
            setlength = {setlength}
            graphWidth = {graphWidth}
            />
        </Flex>
        <Flex className="column" width="60%">
          <GraphArea
            onClicked={onClicked}
            graphWidth = {graphWidth}
            dataLength  = {dataLength}
            VKToption = {VKToption}
            dataRaw = {dataRaw}
            cellArea_ratio ={cellArea_ratio}
            caseOption = {caseOption}
            PV_performanceRatio ={PV_performanceRatio}
            PV_CellNominalEffiency ={PV_CellNominalEfficiency}
            text = {text}
          />
        </Flex>
    </Flex>
)
}