import { useState, useEffect } from 'react';
import { json } from 'd3';
import { feature } from 'topojson';

const worldUrl = 'https://unpkg.com/world-atlas@2.0.2/countries-50m.json';
const stateURL = 'https://cdn.jsdelivr.net/npm/us-atlas@3/states-10m.json';

export const useData = () => {
  const [data, setData] = useState(null);

  useEffect(() => {
    json(worldUrl).then((world) => {
      json(stateURL).then((states) => {

      setData({
        state: feature(states, states.objects.states),
        country: feature(world, world.objects.countries),
        
      });
    });
  })
  }, []);

  return data;
};
