import { Tooltip } from '@chakra-ui/react';
import { scaleLinear} from 'd3';
import {FaCrosshairs} from 'react-icons/fa'
import { AxisBottom } from "./AxisBottom";
import {AxisLeft} from './AxisLeft'
import {Line} from './line'
import { OuterRegion } from './outerregion';
import {Points} from './points'
import { LineTags } from './lineTags';




export const Linechart = (
    {studyCase,
    setStudyCase,
    graphWidth,
    casePosition,
    setCasePosition,
    }
    ) => {

    
    const height = 500; 
    //document.body.clientHeight;
    const margin = {top: 50, bottom: 100, left: 100, right: 100}
    const width= graphWidth; 
    const xAxisLabelOffset = 45;
    const yAxisLabelOffset = 65;
    const innerHeight = height - margin.top - margin.bottom
    const innerWidth = width - margin.left -margin.right
    const data = [
        {n_cell:'15%' ,Wp:828},
        {n_cell:'20%' ,Wp:1104},
        {n_cell:'25%' ,Wp:1380},
        {n_cell:'30%' ,Wp:1656},
        {n_cell:'35%' ,Wp:1932}
    ]

    const cars = [
        {Automaker_Model:'Toyota Prius 2009',PV_nominal_power_Wp: 56, PV_cell_eff:	16.5, Equiv_PV_cell_area_m2:	0.339393939},
        {Automaker_Model:'Toyota Prius 2017',PV_nominal_power_Wp: 180, PV_cell_eff:	22.5, Equiv_PV_cell_area_m2:	0.8},
        {Automaker_Model:'Toyota Prius 2019',PV_nominal_power_Wp: 860, PV_cell_eff:	34.9, Equiv_PV_cell_area_m2:	2.464183381},
        {Automaker_Model:'Lightyear One 2019',PV_nominal_power_Wp: 1000, PV_cell_eff:	22, Equiv_PV_cell_area_m2:	4.545454545},
        {Automaker_Model:'Sonomotors Sion 2019',PV_nominal_power_Wp: 1204, PV_cell_eff:	24, Equiv_PV_cell_area_m2:	5.016666667}
    ]

   

    const title = ''

    const xValue =[0,5.52]
    const yAxisLabel = 'PV  Array Nominal Power [Wp]';

    const yValue = d=> d.Wp
    const xAxisLabel = 'Total PV Cell Area [m²]';


    const xScale = scaleLinear()
        .domain(xValue)
        .range([0,innerWidth])


    const yScale = scaleLinear()
        .domain([0,1932])
        .range([innerHeight,0])

    const handleMouseClick = event => {
        const {clientX, clientY} = event
        const absol = document.getElementById('lineBack').getBoundingClientRect()
        setCasePosition({x: (clientX - absol.x)/innerWidth, y: clientY-absol.y})
        setStudyCase([xScale.invert(clientX - absol.x), yScale.invert(clientY-absol.y)])  
    }

    return (
        <>
        <svg className="linechart" height={height} width={width}>
            <g className="linechart" transform ={`translate(${margin.left},${margin.top})`} fontSize="1em">
                
                <AxisBottom xScale={xScale} innerHeight={innerHeight} tickOffset={7}/>
                <text
                    className="axis-label"
                    x={innerWidth / 2}
                    y={innerHeight + xAxisLabelOffset}
                    textAnchor="middle"
                    >
                    {xAxisLabel}
                </text>
                <AxisLeft yScale={yScale} innerWidth={innerWidth}/>
                <text
                    className="axis-label"
                    textAnchor="middle"
                    transform={`translate(${-yAxisLabelOffset},${innerHeight /
                        2}) rotate(-90)`}
                    >
                    {yAxisLabel}
                </text>
                <text
                    className="title"
                    textAnchor="middle"
                    transform={`translate(${innerWidth/2},${-margin.top/3})`}
                    >
                    {title}
                </text>

                <path
                className="xydomain"
                fill={'none'} 
                d={`M0,0V${innerHeight} H${innerWidth}`}
                stroke ={"black"}
                />

                <LineTags
                    data= {data}
                    xScale= {xScale} 
                    yScale= {yScale} 
                />
                <g  className={"clickable"} onClick={d => handleMouseClick(d)}>
                <rect id="lineBack" width={innerWidth} height={innerHeight} fill='rgba(235, 238, 238, 0.5)' />
                <Line
                    data= {data}
                    xScale= {xScale} 
                    yScale= {yScale} 
                />
                <OuterRegion 
                    data={data} 
                    innerHeight={innerHeight} 
                    xScale={xScale} 
                    yScale={yScale}
                    graphWidth={graphWidth}
                    />
                <Points
                    cars= {cars}
                    xScale= {xScale} 
                    yScale= {yScale} 
                    studyCase = {studyCase}
                    margin={margin}
                    offset={[xAxisLabelOffset,yAxisLabelOffset]}
                />
                {/* <Tooltip label={"Custom Case"} hasArrow textAlign="center">
                <g> */}
                <FaCrosshairs
                        x = {-14.5+ casePosition.x*innerWidth}
                        y = {-15+ casePosition.y }
                        size = {"30px"}
                        fill = {"#2B6CB0"}
                        text-align = {"centre"}
                    />
                {/* <circle
                    cx = {casePosition.x*innerWidth}
                    cy = {casePosition.y }
                    r = {15}
                    fill = {"white"}
                    opacity={0}
                    stroke={"none"}
                />
                </g>
                </Tooltip> */}
                </g>
            </g>
        </svg>
        
        </>
    )
}
