import { Text, Tooltip, Icon,Flex} from "@chakra-ui/react"
import {AiOutlineInfoCircle} from 'react-icons/ai'
// import EVImage from './EV.png'
// import ICEVImage from './ICEV.png'
// import PVImage from './PVONEV.png'
export const Intro = ({text}) => (
    <Flex marginInline="2%" padding="3%" className="row" background="white">
    <Text >
      {text.p1}
      </Text>
      <br/>
      <Text >
      {text.p2}
      </Text>
      <br/>
      <Text>
        {text.p3}
      </Text>
      <br/>
      <Text>
        I have tried to design this website so that it can be easily understood by anyone, and so I have left some helpful
        pointers. Whenever there is a 
      <Tooltip hasArrow  textAlign="top" padding="10px" borderRadius='7px' label={"Hello there!"}>
      <span>
      <Icon as={AiOutlineInfoCircle} paddingBottom="1px" marginInline="5px" _hover={{opacity:0.3}}/> 
      </span>
      </Tooltip>
          next to something, you can hover over it and it will give small explanation of what it does. Have a go now!
      </Text>
    </Flex>
)