import {
  Popover,
  PopoverTrigger,
  PopoverContent,
  PopoverCloseButton,
  PopoverArrow,
  PopoverHeader,
  Button,
  PopoverBody,
  PopoverFooter
} from "@chakra-ui/react"


import {BsGearFill } from 'react-icons/bs'
import {FaGlobe,FaGlobeEurope} from "react-icons/fa"
export const MapOptions = ({
    countries,
    setCountries,
    setMapClicked,
    G20EUR,
    arraySame,
}) => {
    
    let temp;
    return (
        <Popover>
            <PopoverTrigger>
                <Button 
                    marginInline={"2%"}
                    colorScheme="teal"
                    leftIcon={<BsGearFill />}
                > Selection Options </Button>
            </PopoverTrigger>
            <PopoverContent>
            <PopoverArrow/>
            <PopoverCloseButton />
            <PopoverHeader>Selection by Group</PopoverHeader>
            <PopoverBody>

            <Button
                alignSelf ="center"
                leftIcon={<FaGlobe/>}
                margin={"2%"}
                mr={3}
                colorScheme="orange" 
                onClick={() =>{
                  if (arraySame([G20EUR,countries]) || countries===[]) {
                    setCountries(countries.filter( d => !G20EUR.includes(d)))}
                  else { 
                      temp = G20EUR.filter( d => !countries.includes(d))
                      setCountries([...countries,...temp])
                    }
              setMapClicked(true)
            }}>{arraySame([G20EUR,countries]) ?'Deselect G20' :' Select G20'}
            </Button>
            
            </PopoverBody>
            <PopoverFooter></PopoverFooter>
            </PopoverContent>
            </Popover>
    )
}