
export const ColourLegend = ({
    colorScale,
    selection,
    duration,
    position,
    data,
    radius,
    legHov,
    onLegHov,
    legClick,
    onLegClick,
    prevData,
  }) => {
    const legend  = selection.selectAll('.legend')
        .data([null])
        .join((enter) => {
          let g = enter.append('g')
        .classed('legend',true)
        .attr("transform", `translate${position}`)
        .style('z-index', '10')
        
        g.append('rect')
        .attr('x', -radius * 2)
        .attr('y', -radius * 2)
        .attr('rx', 5)
        .attr('width',160)
        .attr('height',radius*8)
        .style('fill','#f8f8ff')
        .attr('opacity',0.9)
        
        return g
        },
        (update) =>
        update,
      (exit) =>{
      exit
      .transition()
      .duration(duration)
      .remove()}
    )

    if (data[0] !== prevData) { 
        legend.selectAll('.leg').remove()}

    legend
    .selectAll(".leg")
    .data(data)
    .join((enter) => {
       let g = enter.append("g")
        .classed("leg", true)
        .attr('transform',(d,i)=> `translate(0,${i*radius*4})`)

        g.append('rect')
        .attr('x', -radius * 2)
        .attr('y', -radius * 2)
        .attr('width', 160)
        .attr('height', radius*4)
        .style('fill','#fff')
        .attr('opacity',0.1)

        g.append("circle")
        .style("fill",(d,i) => colorScale(i))
        .attr("r",0)

        g.append('text')
        .attr("opacity",0)
        .text(d => d)
        .attr("transform", `translate(${radius*2 },0)`)
        .attr("dy",".32em")
        
        return g
        },
    (update) =>
        update,
      (exit) =>{
      exit
      .transition()
      .duration(duration)
      .remove()}
    )
    .on('mouseover',  d => {
        onLegHov(d.target.__data__)
        })
    .on('click', d => {
        if (legClick !== d.target.__data__) {
            onLegClick(d.target.__data__)
        }
        else{
            onLegClick(null)
        }
        onLegClick(d.target.__data__)
    })
    .on('mouseout',
        d => {
            onLegHov(null)
        })
    .transition()
    .duration(duration*0.1)
    .attr('opacity', d => {
        if (!legClick && !legHov)
            {return 1}
        else {
            if (legClick === d || legHov === d)
            {return 1}
                else {return 0.3}}
    })
    legend.selectAll(".leg").selectAll("circle")
        .transition()
        .duration(duration)
        .attr("r",radius)

    legend.selectAll(".leg").selectAll("text")
        .transition()
        .duration(duration)
        .attr("opacity",1)
}