import {
  Popover,
  PopoverTrigger,
  PopoverContent,
  PopoverCloseButton,
  PopoverArrow,
  PopoverHeader,
  Button,
  PopoverBody,
  PopoverFooter
} from "@chakra-ui/react"


import {BsGearFill } from 'react-icons/bs'
import {FaGlobe,FaGlobeAsia,FaGlobeEurope} from "react-icons/fa"
export const MapOptions = ({
    countries,
    setCountries,
    setMapClicked,
    G20,
    EUR,
    ASIA,
    onClose,
    arraySame,
    buttonWidth,
}) => {
    
    let temp;
    return (
        <Popover>
            <PopoverTrigger>
                <Button 
                    marginInline={"2%"}
                    colorScheme="teal"
                    leftIcon={<BsGearFill />}
                > Selection Options </Button>
            </PopoverTrigger>
            <PopoverContent>
            <PopoverArrow/>
            <PopoverCloseButton />
            <PopoverHeader>Selection by Group</PopoverHeader>
            <PopoverBody>
            <Button
              alignSelf ="center"
              margin={"2%"}
              leftIcon={<FaGlobeEurope/>}
              mr={3}
              colorScheme="blue" 
              onClick={() =>{
                if (arraySame([EUR,countries]) || countries===[]) {
                    setCountries(countries.filter( d => !EUR.includes(d)))}
                 else { 
                     temp = EUR.filter( d => !countries.includes(d))
                     setCountries([...countries,...temp])
                   }
                 setMapClicked(true)
            }}>{arraySame([EUR,countries]) ?'Deselect Europe' :'Select All Europe'}
            </Button>

            <Button
                alignSelf ="center"
                leftIcon={<FaGlobe/>}
                margin={"2%"}
                mr={3}
                colorScheme="orange" 
                onClick={() =>{
                  if (arraySame([G20,countries]) || countries===[]) {
                    setCountries(countries.filter( d => !G20.includes(d)))}
                  else { 
                      temp = G20.filter( d => !countries.includes(d))
                      setCountries([...countries,...temp])
                    }
              setMapClicked(true)
            }}>{arraySame([G20,countries]) ?'Deselect G20' :' Select All G20'}
            </Button>
            <Button
                alignSelf ="center"
                margin={"2%"}
                leftIcon={<FaGlobeAsia/>}
                mr={3}
                colorScheme="green" 
                onClick={() =>{
                  if (arraySame([ASIA,countries]) || countries===[]) {
                    setCountries(countries.filter( d => !ASIA.includes(d)))}
                  else { 
                      temp = ASIA.filter( d => !countries.includes(d))
                      setCountries([...countries,...temp])
                    }
              setMapClicked(true)
            }}>{arraySame([ASIA,countries]) ?'Deselect Asia' :' Select All Asia'}
            </Button>
            </PopoverBody>
            <PopoverFooter></PopoverFooter>
            </PopoverContent>
            </Popover>
    )
}