import { WorldCreator } from "./World/WorldCreator"
import {StatesCreator} from "./States/StatesCreator"
import {EuropeCreator} from "./Europe/EuropeCreator"

export const ModalSelector = ({
    setlength,
    onClicked,
    setOnClicked,
    data,
    onClose,
    mapClicked,
    setMapClicked,
    countries,
    setCountries,
    mapSelect,
    width,
    height,
}) => {
    const regionsAvailable = ["South Africa", "Indonesia", "Saudi Arabia", "Australia", "India", "China", "Japan", "South Korea", "Turkey", "Mexico", "United States of America", "Germany", "Russia", "Argentina", "Italy", "Europe", "United Kingdom", "Canada", "Brazil", "France"];    
    if (mapSelect === "World")
    return (
        <WorldCreator
            setlength = {setlength}
            onClicked = {onClicked}
            setOnClicked = {setOnClicked}
            data = {data}
            onClose = {onClose}
            mapClicked = {mapClicked}
            setMapClicked= {setMapClicked}
            countries = {countries}
            setCountries = {setCountries}
            regionsAvailable = {regionsAvailable}
        />
    )
    else if (mapSelect === "United States")
    return(
        <StatesCreator
            setlength = {setlength}
            onClicked = {onClicked}
            setOnClicked = {setOnClicked}
            data = {data}
            onClose = {onClose}
            mapClicked = {mapClicked}
            setMapClicked= {setMapClicked}
            countries = {countries}
            setCountries = {setCountries}
            regionsAvailable = {regionsAvailable}
        />
    )
    else if (mapSelect === "Europe")
    return (
        <EuropeCreator
            setlength = {setlength}
            onClicked = {onClicked}
            setOnClicked = {setOnClicked}
            data = {data}
            onClose = {onClose}
            mapClicked = {mapClicked}
            setMapClicked= {setMapClicked}
            countries = {countries}
            setCountries = {setCountries}
            height={height}
            width= {width}
            regionsAvailable = {regionsAvailable}
        />
    )
}