import {
  Flex,
  Slider,
  SliderTrack,
  SliderFilledTrack,
  SliderThumb,
  NumberInput,
  NumberInputField,
  Button,
  Popover,
  PopoverTrigger,
  PopoverContent,
  PopoverArrow,
  Tooltip,
  Center,
  Icon,
} from "@chakra-ui/react"
import { useState } from "react";
import { Dropdown } from "./DropdownChak";
import {AiOutlineInfoCircle} from 'react-icons/ai'
export const CaseSelector = ({
    options,
    selectedValue,
    onSelectedValueChange,
    label,
    tooltip
}) => {

const [valueChange, onValueChange] = useState(selectedValue[2])
const [valueChange1, onValueChange1] = useState(selectedValue[3])
const dropChange = d => {
    let p = selectedValue[2]
    let q = selectedValue[3]
    let temp = [d,false,p,q]
    onSelectedValueChange(temp)
}
return (
    <Flex className="buttonSpace" width='90%'>
    <Flex className="column" width = "40%"  justifyContent="center">
    <Dropdown
        label = {label}
        options={options}
        id = {'menuDrop'}
        selectedValue={selectedValue[0]}
        onSelectedValueChange={dropChange}
        width={"90%"}
        tooltip = {tooltip}
    />
    </Flex>
    <Center width ="20%">
    <Popover>
    <PopoverTrigger>
     <Button 
        colorScheme={"green"}
        width="100%" 
        height={10} 
        fontSize={"0.9rem"} 
        marginInline="2%" 
        className="buttonSpace" 
        >
        Custom
     </Button>
     </PopoverTrigger>
     <PopoverContent width="600px">
     <PopoverArrow />
    <Flex className="buttonSpace" width='100%' >
    <label style={{paddingTop:"6px"}} display="flex" flex-direction="row"> {"EV Energy Consumption:"} </label>
        <Slider
            width="40%" 
            marginInline="auto"
            focusThumbOnChange={false} 
            min={100} 
            max={300}
            value={valueChange} 
            onChange = {(val) => onValueChange(val)}
            onChangeEnd={(val) => {
            let d = selectedValue[0]
            let q = selectedValue[3]
            let temp = [d,true,val,q]
            onSelectedValueChange(temp)
            }}>
            <SliderTrack bg="blue.100">
                <SliderFilledTrack />
            </SliderTrack>
            <SliderThumb boxSize={3.5} />
        </Slider>
            <NumberInput
                 min={100} 
                 max={300}  
                 width="100px" 
                 value={valueChange + 'Wh/km'}
                 marginLeft = "auto" 
                 onChange={ (val) => {
                 onValueChange(val)
                 }}
                 onBlur={ () => {
                    let d = selectedValue[0]
                    let q = selectedValue[3]
                    let temp = [d,true,valueChange,q]
                    onSelectedValueChange(temp)
                    }} > 
                <NumberInputField bg="white" style={{padding:0 }}/>
            </NumberInput>
    </Flex>
    <Flex className="buttonSpace" width='100%' >
    <label style={{paddingTop:"6px"}} display="flex" flex-direction="row"> {"EV Vehicle Footprint:"} </label>
        <Slider
            width="40%" 
            marginInline="auto"
            focusThumbOnChange={false} 
            min={2} 
            max={5}
            value={valueChange1} 
            onChange = {(val) => onValueChange1(val)}
            onChangeEnd={(val) => {
            let d = selectedValue[0]
            let q = selectedValue[2]
            let temp = [d,true,q,val]
            onSelectedValueChange(temp)
            }}>
            <SliderTrack bg="blue.100">
                <SliderFilledTrack />
            </SliderTrack>
            <SliderThumb boxSize={3.5} />
        </Slider>
            <NumberInput
                 min={2} 
                 max={5} 
                 marginLeft="auto"
                 width="100px" 
                 value={valueChange1 + 'm²'}
                 onChange={ (val) => {
                 onValueChange1(val)
                 }}
                 onBlur={ () => {
                    let d = selectedValue[0]
                    let q = selectedValue[2]
                    let temp = [d,true,q,valueChange1]
                    onSelectedValueChange(temp)
                    }} > 
                <NumberInputField bg="white" style={{padding:0 }}/>
            </NumberInput>
    </Flex>
    </PopoverContent>
    </Popover>
    </Center>
    </Flex>
)

}
export const VKTSelector = ({
    selectedValue, 
    onSelectedValueChange,
    min,
    max, 
    label, 
    tooltip,
}) => {
    const [valueChange, onValueChange] = useState(selectedValue[0])
    return (
    <>
    <Flex className="buttonSpace" width='90%'>
    <Center>
     <label width="25%" display="flex" flex-direction="row"> {label} </label>
            <Tooltip hasArrow  textAlign="top" label={tooltip} padding="10px" borderRadius='7px' >
            <Center w="20px" h="20px" verticalAlign="middle">
            <Icon as={AiOutlineInfoCircle} paddingBottom="1px" marginInline="5px" _hover={{opacity:0.3}}/> 
            </Center>
            </Tooltip>
            </Center>
     <Button 
        colorScheme="green" 
        width="35%" 
        marginInline="2%" 
        fontSize={"0.9rem"}
        className="buttonSpace" 
        onClick ={() => onSelectedValueChange([16000,true])}
     > 
        {document.body.clientWidth/11 > 90 ?"Region Specific":"Region"} 
     </Button>

    <Popover>
    <PopoverTrigger>
     <Button 
        colorScheme="green" 
        width="30%" 
        fontSize={"0.9rem"} 
        marginInline="2%" 
        className="buttonSpace" 
        
        >
        {document.body.clientWidth/11 > 90 ?"Custom Average":"Custom"} 
     </Button>
     </PopoverTrigger>
     <PopoverContent width="300px">
     <PopoverArrow />
    <Flex className="buttonSpace" width='100%' >
        <Slider
            width="50%" 
            marginInline="4%"
            focusThumbOnChange={false} 
            min={min} 
            max={max}
            value={valueChange} 
            onChange = {(val) => onValueChange(val)}
            onChangeEnd={(val) => {
            onSelectedValueChange([val,false])
            }}>
            <SliderTrack bg="blue.100">
                <SliderFilledTrack />
            </SliderTrack>
            <SliderThumb boxSize={3.5} />
        </Slider>
            <NumberInput
                 min={min} 
                 max={max}  
                 width="40%" 
                 value={valueChange + ' km'}
                 marginInline="2%" 
                 onChange={ (val) => {
                 onValueChange(val)
                 }}
                 onBlur={ () => {
                    onSelectedValueChange([valueChange,false])
                    }} > 
                <NumberInputField bg="white" style={{padding:0 }}/>
            </NumberInput>
    </Flex>
    </PopoverContent>
    </Popover>
    </Flex>

    </>
    )

}

export const Sliders2 = ({selectedValue, onSelectedValueChange,min,max, label,tooltip}) => {
    const [valueChange, onValueChange] = useState(selectedValue)
    return (
    <>
    <Flex className="buttonSpace" verticalAlign="middle"  width='90%'>
    <label style={{paddingTop:"6px"}} width="100px" display="flex" flex-direction="row"> {label} </label>
            <Center paddingBottom="1px">
            <Tooltip  textAlign="top" padding="7px" borderRadius='7px' label={tooltip}>
            <Center w="20px" h="20px" verticalAlign="middle">
             <Icon as={AiOutlineInfoCircle} marginInline="5px" _hover={{opacity:0.3}}/> 
            </Center>
            </Tooltip>
            </Center>
        <Slider 
            width="35%" 
            ml="4%" 
            mr="4%" 
            focusThumbOnChange={false} 
            step={0.1} 
            min={min} 
            max={max}
            value={valueChange} 
            onChange = {(val) => onValueChange(val)}
            onChangeEnd={(val) => onSelectedValueChange(val)}
        >
            <SliderTrack bg="blue.100">
                <SliderFilledTrack />
            </SliderTrack>
            <SliderThumb boxSize={3.5} />
        </Slider>
            <NumberInput 
                min={min} 
                max={max}
                precision={1}
                width="60px" 
                value={valueChange + '%'} 
                onChange={(val) => onValueChange(val)}
                onBlur={ () => {
                 onSelectedValueChange(valueChange)
                 }}
                 >
                <NumberInputField bg="white" style={{paddingRight:0, paddingLeft:"10%"}}/>
            </NumberInput>
    </Flex>
    </>
    )
};
