import { Flex,Text,Icon,Tooltip } from "@chakra-ui/react"
import {AiOutlineInfoCircle} from "react-icons/ai"

export const InfoCalc = ({totalCellArea,nominalPower,vehicle_footprint_m2,graphWidth}) => {
    return (
        <Flex background="#003E74" color="white" alignSelf="center" fontWeight="bold" width="100%" height={(graphWidth>580)?("10px"):((graphWidth>520)?("70px"):("100px"))} paddingBlock="30px" display="flex" direction="row" justifyContent="center" alignItems="center">
           <Text paddingInline="20px" textAlign="center">{`PV Nominal Power: ${nominalPower.toFixed(2)} Wp`}  </Text> 
            <Text paddingInline="20px" textAlign="center">{` EV Area available for PV Integration: ${vehicle_footprint_m2.toFixed(2)} m²`}</Text>
            <Text paddingInline="20px" textAlign="center">{` Total Cell-Area: ${totalCellArea.toFixed(2)} m²`}</Text>
            <Tooltip hasArrow  textAlign="top" padding="10px" borderRadius='7px' label={"Nominal power refers to the power output of the EV with PV, and the cell-area refers to the area of the car covered by PV cells. These variables are used are calculated from the variables above."}>
            <span>
            <Icon as={AiOutlineInfoCircle} paddingBottom="1px" marginInline="5px" _hover={{opacity:0.3}}/> 
            </span>
            </Tooltip>
        </Flex>
    )
}