import {
    select,
    scaleBand,
    scaleLinear,
    axisBottom,
    axisLeft,
    min,
    max,
    scaleOrdinal,
    selection,

} from "d3";


import React, { useRef, useEffect } from "react";
import { ColourLegend } from "./colourLegend2";

selection.prototype.moveToFront = function() {
  return this.each(function(){
  this.parentNode.appendChild(this);
  });
};
export const MultinegbarChart = ({ 
    graphWidth,
    dataOriginal,  
    onClickValue,
    xAxisTitle,
    yAxisTitle,
    Title,
    xValue,
    yValue1,
    yValue2,
    xTitleOffset,
    yTitleOffset,
    titleOffset,
    colorTheme,
    country,
    toolTipText1,
    formatNumber1,
    toolTipText2,
    formatNumber2,
    height,
    dataLength,
    legHov,
    onLegHov,
    legClick,
    onLegClick,
    prevData,
    setPrev,
    chartColour,
    axisSize,
    margin,
}) => {

  const duration = 1000;
  const separation = 2;
  const width= graphWidth;
  const data = dataOriginal.filter(d => onClickValue.includes(country(d)))
  const innerWidth = width - margin.left - margin.right;
  const innerHeight = height - margin.top - margin.bottom;
  const ref = useRef();
  const labels = [toolTipText1,toolTipText2]

  let length;
  if (data.length > 0) 
    length = data.length
  else length = dataLength

  useEffect(() => {
    draw();
    setPrev(toolTipText1)
  }, [data]); 

  const draw = () => {
    
    let maxY = 1;
    if (max(data, yValue1) > max(data, yValue2))
       maxY = max(data,yValue1);
    else  maxY = max(data,yValue2);
    let minY;
    if (min(data, yValue1) > min(data, yValue2))
      if (0 > min(data, yValue2))
          minY = min(data,yValue2);
      else minY = 0;
    else  if (0 > min(data, yValue1))
          minY = min(data,yValue1);
    else minY = 0;;


    const xScale = scaleBand()
      .domain(data.map(xValue))
      .range([0, innerWidth])
      .padding(0.2)
    const bandwidth = xScale.bandwidth()
    const yScale = scaleLinear()
      .domain([minY, maxY])
      .range([innerHeight, 0])
      .nice();

    const xAxis = axisBottom().scale(xScale).tickPadding(10) ;

    const yAxis = axisLeft().scale(yScale).tickSizeInner(-innerWidth).tickPadding(10);

    const colorScale = scaleOrdinal(colorTheme);

    const svg = select(ref.current);
    const chart = svg.select("g");

    const tooltip = select('.tooltip')
        .style('position', 'absolute')
        .style('z-index', '10')
        .style('visibility', 'hidden')
        .style('padding', '10px')
        .style('background', 'rgba(0,0,0,0.6)')
        .style('border-radius', '4px')
        .style('color', '#fff')
        .text('a simple tooltip');

        chart//background
        .selectAll(".background")
        .data([null])
        .join('rect')
        .classed('background',true)
        .attr('width',innerWidth)
        .attr('height',innerHeight)
        .style('fill',chartColour)
        .on('click', () => {
          onLegClick(null)
        })
  
      const xAxisGroup = chart.selectAll(".xAxisGroup").data([null]).join("g").classed("xAxisGroup",true)
      const yAxisGroup = chart.selectAll(".yAxisGroup").data([null]).join("g").classed("yAxisGroup",true)
  
      xAxisGroup//x-axis
      .selectAll(".x-axis")
      .data([null])
      .join("g")
      .classed("x-axis", true)
      .style('z-index', '5')
      .style("font-size", d => `${Math.log(bandwidth)*2.6}px`)
      .attr("transform", `translate(0,${innerHeight})`)
      .transition()
      .duration(duration)
      .call(xAxis)

  xAxisGroup//x-axis title
    .selectAll(".x-axis-title")
    .data([xAxisTitle])
    .join("text")
    .classed("x-axis-title", true)
    .attr("x", innerWidth / 2)
    .attr("y", innerHeight + xTitleOffset)
    .attr("fill", "black")
    .style("text-anchor", "middle")
    .style("font-size", axisSize)
    .text((d) => d);

    yAxisGroup//y-axis
    .selectAll(".y-axis")
    .data([null])
    .join((enter) =>
      enter
        .append("g")
        .style("font-size", "0.9em")
        .classed("y-axis", true)
        .attr("transform", "translate(0,0)"),
      (update) =>
        update,
        (exit) => 
        exit
        .transition()
        .duration(duration)
        .remove()
    )
    .transition()
    .duration(duration)
    .call(yAxis);


    yAxisGroup//y-axis title
      .selectAll(".y-axis-title")
      .data([yAxisTitle])
      .join("text")
      .classed("y-axis-title", true)
      .attr("x", 0)
      .attr("y", yTitleOffset)
      .style("font-size", axisSize)
      .attr("transform", `translate(${yTitleOffset}, ${innerHeight / 2}) rotate(-90)`)
      .attr("fill", "#000")
      .style("text-anchor", "middle")
      .text((d) => d);

    yAxisGroup//y-axis
      .selectAll(".xydomain")
      .data([null])
      .join((enter) =>
        enter
          .append("path")
          .classed("xydomain", true),
        (update)=>
          update
          ,
          (exit) => 
          exit
          .transition()
          .duration(duration)
          .remove()
      )      
      .append("path")
      .transition()
      .duration(duration)
      .attr('fill','none')
      .attr('d',`M0,0V${innerHeight} H${innerWidth}`)
      .attr('stroke',"black")

      chart //chart title
          .selectAll(".chart-title")
          .data([Title])
          .join("text")
          .classed("chart-title",true)
          .attr("x", innerWidth / 2)
          .attr('y',titleOffset)
          .attr("fill", "#000")
          .style("font-size", "14px")
          .style("text-anchor", "middle")
          .text((d) => d);


    const bars1 = chart//bar 1
      .selectAll(".bar1")
      .data(data)
      .join((enter) =>
        enter
          .append("rect")
          .classed("bar1", true)
          .attr("y", (d) => yScale(0))
          .attr("height", 0),
        (update) =>
          update,
          (exit) => 
          exit
          .transition()
          .duration(duration)
          .delay((d, i) => ((length-i) * duration) / length)
          .attr("height", 0)
          .attr("y", innerHeight)
          .remove()
      )
      .attr("x", (d) => xScale(xValue(d)))
      .style("fill", (d, i) => colorScale(0))
      .attr("width", (d) => xScale.bandwidth()/2 - separation/2)
      .on('mouseover',  (d, i) => {
        const temp = d.target.__data__
        tooltip
          .html(
            `<div>Country: ${country(temp)}</div><div>${toolTipText1 + yValue1(temp).toFixed(2)} ${formatNumber1}</div>`
          )
          .style('visibility', 'visible');
        select(this).transition().attr('fill', "#eec42d");
        })
        .on('mousemove', function (d) {
            tooltip
            .style('top', d.pageY -10 + 'px')
            .style('left', d.pageX +10 + 'px');
        })
        .on('mouseout', function () {
            tooltip.html(``).style('visibility', 'hidden');
            select(this).transition().attr('fill', '#437c90');
        })

      bars1
      .transition('bars')
      .duration(duration)
      .delay((d, i) => (i * duration) / length)
      .attr("height", (d) => Math.abs(-yScale(0) + yScale(yValue1(d))))
      .attr("y", (d) => -yScale(0) + yScale(yValue1(d)) > 0 ?yScale(0) :yScale(yValue1(d)));

      const bars2 = chart//bar 2
      .selectAll(".bar2")
      .data(data)
      .join((enter) =>
        enter
          .append("rect")
          .classed("bar2", true)
          .attr("y", (d) => yScale(0))
          .attr("height", 0),
        (update) =>
          update,
          (exit) => 
          exit
          .transition()
          .duration(duration)
          .delay((d, i) => ((length-i) * duration) / length)
          .attr("height", 0)
          .attr("y", innerHeight)
          .remove()
      )
      .attr("x", (d) => xScale(xValue(d))+ xScale.bandwidth()/2 + separation/2)
      .style("fill", (d, i) => colorScale(1))
      .attr("width", (d) => xScale.bandwidth()/2 - separation/2)
      .on('mouseover',  (d, i) => {
        const temp = d.target.__data__
        tooltip
          .html(
            `<div> ${country(temp)}:</div><div>${toolTipText2 + yValue2(temp).toFixed(2)} ${formatNumber2}</div>`
          )
          .style('visibility', 'visible');
        select(this).transition().attr('fill', "#eec42d");
        })
        .on('mousemove', function (d) {
            tooltip
            .style('top', d.pageY -10 + 'px')
            .style('left', d.pageX +10 + 'px');
        })
        .on('mouseout', function () {
            tooltip.html(``).style('visibility', 'hidden');
            select(this).transition().attr('fill', '#437c90');
        })

      bars2
      .transition('bars')
      .duration(duration)
      .delay((d, i) => (i * duration) / length)
      .attr("height", (d) => Math.abs(-yScale(0) + yScale(yValue2(d))))
      .attr("y", (d) => -yScale(0) + yScale(yValue2(d)) > 0 ?yScale(0) :yScale(yValue2(d)));
      
      bars1
      .transition("opac")
      .duration(duration*0.2)
      .attr('opacity', () => {
        if (legClick === labels[0])
            {return 1}
        else {
        if (!!legHov){
          if (legHov  === labels[0])
            {return 1}
          else {return 0.2}
        }
        else if (!legClick && !legHov) {return 1}
        else {return 0.2}
      }}
      )


      bars2
      .transition("opac")
      .duration(duration*0.2)
      .attr('opacity', () => {
        if (legClick === labels[1])
            {return 1}
        else {
        if (!!legHov){
          if (legHov  === labels[1])
            {return 1}
          else {return 0.2}
        }
        else if (!legClick && !legHov) {return 1}
        else {return 0.2}
      }}
      )

      ColourLegend({
        colorScale: colorScale,
        selection: chart,
        duration: duration,
        data: labels,
        position: `(${graphWidth - 330},${25})`,
        radius: 5,
        legHov: legHov, 
        onLegHov : onLegHov,
        legClick: legClick,
        onLegClick: onLegClick,
        prevData: prevData,
      })
      chart.selectAll('.legend').moveToFront()
  };

  return (
    <div id="barchart">
        <svg className="chart" width={width} height={height}  ref={ref}>
            <g transform={`translate(${margin.left}, ${margin.top})`}/>
        </svg>
        <div className="tooltip"/>
    </div>
  );
}