
import { BarChart } from "../D3_Charts/barAni";
import {Scatter} from "../D3_Charts/scatterAni";
import { MultibarChart } from "../D3_Charts/multibarAni";
import { MultinegbarChart } from "../D3_Charts/multinegbarAni";

import { Flex,Text } from "@chakra-ui/layout";
import {schemeAccent, schemeCategory10,} from 'd3'
import { useState } from "react";
export const GraphSelector = ({
    onClicked,
    dataOriginal,
    graph,
    graphWidth,
    dataLength,
    legHov,
    onLegHov,
    legClick,
    onLegClick,
    attributes,
    }) => {
        const height = 500;
        const [prevData, setPrev] = useState(null)
        const [labelOn, setLabel] = useState(true)
        const axisSize = "1em";
        const chartColour = 'rgba(235, 238, 238, 0.5)';
        const yTitleOffset = -27;
        const xTitleOffset = 50;
        const titleOffset = 20;
        const margin = {
            top: 30,
            bottom: 80,
            left: 90,
            right: 20,
          };
    if (graph === attributes[0]) {
        return <Scatter
        graphWidth={graphWidth}
        height = {height}
        dataOriginal={dataOriginal}
        onClickValue={onClicked}
        xAxisTitle = {'PV on EV Emissions [gCO2e/kWh]'}
        yAxisTitle ={'EV Emissions [gCO2e/kWh]'}
        Title = {''}
        xValue = {d => d.PVonEV_usageEmissions}
        yValue  = {d => d.EV_Usage_Emissions}
        
        colorTheme={schemeAccent}
        radius = {5}
        country = {d => d.region}
        toolTipText1 = "x: "
        toolTipText2 = "y: "
        formatNumber1 = ""
        formatNumber2 = ""
        dataLength = {dataLength}
        xTitleOffset = {xTitleOffset}
        yTitleOffset = {yTitleOffset}
        titleOffset = {titleOffset}
        chartColour = {chartColour}
        axisSize = {axisSize}
        margin = {margin}
        abbrev = {d => d.abbrev}
        labelOn = {labelOn}
        setLabel = {setLabel}
        />
        }
    else if (graph === attributes[1]) {
        return <BarChart
        graphWidth={graphWidth}
        height = {height}
        dataOriginal={dataOriginal}
        onClickValue={onClicked}
        xAxisTitle = {''}
        yAxisTitle ={'EV-Usage Emissions Reductions [gCO2e/km]'}
        Title = {''}
        xValue = {d => d.abbrev}
        yValue  = {d => d.EV_Usage_Emissions}
        xTitleOffset = {xTitleOffset}
        yTitleOffset = {yTitleOffset}
        titleOffset = {titleOffset}
        colorTheme={schemeCategory10}
        country = {d => d.region}
        toolTipText = "EV-Usage Emissions Reductions: "
        formatNumber = "gCO2e/km"
        dataLength = {dataLength}
        chartColour = {chartColour}
        axisSize = {axisSize}
        margin = {margin}
        />
        }
    else if (graph === attributes[5]) {
            return <BarChart
            graphWidth={graphWidth}
            height = {height}
            dataOriginal={dataOriginal}
            onClickValue={onClicked}
            xAxisTitle = {''}
            yAxisTitle ={'Annual Global Horizontal Irradiance [kWh/m²]'}
            Title = {''}
            xValue = {d => d.abbrev}
            yValue  = {d => d.Annual_GHI_kWh_m2}
            xTitleOffset = {xTitleOffset}
            yTitleOffset = {yTitleOffset}
            titleOffset = {titleOffset}
            colorTheme={schemeCategory10}
            country = {d => d.region}
            toolTipText = "GHI: "
            formatNumber = "kWh/m²"
            dataLength = {dataLength}
            chartColour = {chartColour}
            axisSize = {axisSize}
            margin = {margin}
            />
            }
    else if (graph === attributes[2]) {
        return <MultibarChart
        graphWidth = {graphWidth}
        height={height}
        dataOriginal={dataOriginal}
        onClickValue={onClicked}
        xAxisTitle = {''}
        yAxisTitle ={'Carbon Intensity of Electricity Grid [gCO2e/kWh]'}
        Title = {''}
        xValue = {d => d.abbrev}
        yValue1  = {d => d.CIEG2013_gCO2e_kWh}
        yValue2 = {d => d.CIEG_gCO2_kWh_2018}
        xTitleOffset = {xTitleOffset}
        yTitleOffset = {yTitleOffset}
        titleOffset = {titleOffset}
        colorTheme={['orange','red']}
        country = {d => d.region}
        toolTipText1 = "CIEG (2013): "
        toolTipText2 = "CIEG (2018): "
        formatNumber1 = "gCO2e/kWh"
        formatNumber2 = "gCO2e/kWh"
        dataLength = {dataLength}
        chartColour = {chartColour}
        axisSize = {axisSize}
        margin = {margin}
        legHov = {legHov}
        onLegHov = {onLegHov}
        legClick = {legClick}
        onLegClick = {onLegClick}
        position= {`(${graphWidth - 320},${25})`}
        prevData = {prevData}
        setPrev = {setPrev}
        />
        }
    else if (graph === attributes[3]) {
        return <MultibarChart
        graphWidth = {graphWidth}
        height = {height}
        dataOriginal={dataOriginal}
        onClickValue={onClicked}
        xAxisTitle = {''}
        yAxisTitle ={'VKT/day [km/day]'}
        Title = {''}
        xValue = {d => d.abbrev}
        yValue1  = {d => d.VKT_day}
        yValue2 = {d => d.PV_only_drivingRange_km_day}
        xTitleOffset = {xTitleOffset}
        yTitleOffset = {yTitleOffset}
        titleOffset = {titleOffset}
        colorTheme={['#02893B','#BBCE00']}
        country = {d => d.region}
        toolTipText1 = "Average VKT/day: "
        toolTipText2 = "PV Only Range: "
        formatNumber1 = "km/day"
        formatNumber2 = "km/day"
        dataLength = {dataLength}
        chartColour = {chartColour}
        axisSize = {axisSize}
        margin = {margin}
        abbrev = {d => d.abbrev}
        legHov = {legHov}
        onLegHov = {onLegHov}
        legClick = {legClick}
        onLegClick = {onLegClick}
        position = {`(${graphWidth - 330},${25})`}
        prevData = {prevData}
        setPrev = {setPrev}
        />
        }
    else if (graph === attributes[4]) {
        return <MultinegbarChart
        height={height}
        graphWidth = {graphWidth}
        dataOriginal={dataOriginal}
        onClickValue={onClicked}
        xAxisTitle = {''}
        yAxisTitle ={'CIEG Decrease [gCO2e/kWh]'}
        Title = {''}
        xValue = {d => d.abbrev}
        yValue1  = {d => -d.CIEG_trends_gCO2e_kWh}
        yValue2 = {d => -d.CIEG_PV_change_trends_gCO2e_kWh}
        xTitleOffset = {xTitleOffset}
        yTitleOffset = {yTitleOffset}
        titleOffset = {titleOffset}
        colorTheme={['orange','red']}
        country = {d => d.region}
        toolTipText1 = "CIEG Change: "
        toolTipText2 = "CIEG PV Change: "
        formatNumber1 = "gCO2e/kWh"
        formatNumber2 = "gCO2e/kWh"
        dataLength = {dataLength}
        chartColour = {chartColour}
        axisSize = {axisSize}
        margin = {margin}
        abbrev = {d => d.abbrev}
        legHov = {legHov}
        onLegHov = {onLegHov}
        legClick = {legClick}
        onLegClick = {onLegClick}
        prevData = {prevData}
        setPrev = {setPrev}
        />
        }
    else {
        return <Flex alignItems="center" width={document.body.clientWidth/2 }><Text>invalid</Text></Flex>
    }
}
    