
export const AxisBottom = ({xScale, innerHeight}) => 
    xScale.ticks().map(tickValue => ( 

    <g 
        className = "tick"
        key={tickValue} 
        transform={`translate(${xScale(tickValue)},0)`}
    >
    
    <line 
        y2= {innerHeight} 
        stroke="black"
    />
    
    <text 
        y={innerHeight + 5} 
        style={{textAnchor:"middle" }} 
        dy="0.73em"
        > {tickValue} 
    
    </text> 

    </g>               
))