
import {geoMercator, geoPath, geoGraticule,select,zoom } from 'd3';

let temp;



export const Countries = ({ 
    data: { country },
    setCountries,
    countries,
    mapClicked,
    setMapClicked,
    regionsAvailable,
    height,
    width,

  }) => {

    const projection = geoMercator().center([ 13, 52 ])
    .translate([ width/2, height/2 ])
    .scale([ width/1.5 ]);


    const path = geoPath(projection);
    const graticule = geoGraticule();

    const selection =  select(".map-container")
    const gMap = select(".shapes")
    selection.call(zoom()
    .scaleExtent([0.9, 10])
    .translateExtent([[0, -40], [900,500]])
    .on('zoom', d => {
        gMap.attr("transform", d.transform);
      }));
    let temp2;

    
    const tooltip = select('.map-tooltip')
    .style('position', 'absolute')
    .style('z-index', '10')
    .style('visibility', 'hidden')
    .style('padding', '10px')
    .style('background', 'rgba(0,0,0,0.6)')
    .style('border-radius', '4px')
    .style('color', '#fff')
    .text('a simple tooltip');

   return (
    <>
    <path className="graticules" d={path(graticule())} />
    {country.features.map(feature => (
        <path 
          className='country' 
          id= {feature.properties.name} 
          key={feature.id} 
          d={path(feature)} 
          fill={regionsAvailable.includes(feature.properties.name) ?'#137B80' :'#ebf4f4'}
          onClick={() => {

            if (regionsAvailable.includes(feature.properties.name)) {
              let a = countries.indexOf(feature.properties.name)
              if (countries.includes(feature.properties.name))
                  temp = countries.filter( d => countries.indexOf(d) !== a)
              else  temp = [...countries,feature.properties.name]
                  setCountries(temp)
              }
            else console.log('Data not available !')
          }}
          strokeWidth={0.09}
          opacity={countries.includes(feature.properties.name) ?0.6 :1}
          stroke="black"
          onMouseOver={ d => {
            temp2 = d.target.id
            tooltip.html(
              `<div>${temp2}</div>`
            )
            .style('visibility', 'visible')
            select(this).transition().attr('fill', "#eec42d");
            }}
          onMouseMove = {(d) => {
            tooltip
            .style('top', d.nativeEvent.layerY + 30 + 'px')
            .style('left',  d.nativeEvent.layerX + 'px');
          }}
          onMouseOut={ () => {
              tooltip.html(``).style('visibility', 'hidden');
              select(this).transition().attr('fill', '#437c90');
            }}
          />
    ))} /
    <rect height="100%" width="100%" fill={mapClicked ?'none':'grey'} opacity="0.3" onClick={() => {setMapClicked(true)}}/>
    <text x={'50%'} y={'45%'} textAnchor="middle"  className="pretext" fill={'none'} stroke={mapClicked ?'none':'black'} onClick={() => {setMapClicked(true)}} >
      Drag around the map and click to select a country.
      </text>
    <text x={'50%'} y={'50%'} textAnchor="middle" fill={'none'} stroke={mapClicked ?'none':'black'}>
      Click here to start.
      </text>
    </>
)
};
