import { selection } from "d3";
import {
    select,
    scaleBand,
    scaleLinear,
    axisBottom,
    axisLeft,
    max,
    scaleOrdinal,
    format,
} from "d3";



import React, { useRef, useEffect } from "react";



export const BarChart = ({
    graphWidth,
    dataOriginal,  
    onClickValue,
    xAxisTitle,
    yAxisTitle,
    Title,
    xValue,
    yValue,
    xTitleOffset,
    yTitleOffset,
    titleOffset,
    colorTheme,
    country,
    toolTipText,
    formatNumber,
    height,
    dataLength,
    chartColour,
    axisSize,
    margin,
}) => {
  const duration = 1000;
  const width= graphWidth; 
  const data = dataOriginal.filter(d => onClickValue.includes(country(d)))
  const innerWidth = width - margin.left - margin.right;
  const innerHeight = height - margin.top - margin.bottom;
  const ref = useRef();
  xTitleOffset += -15

  let length;
  if (data.length > 0) 
    length = data.length
  else length = dataLength
  
  
  useEffect(() => {
    draw();
  }, [data]); 

  const draw = () => {
    
    const xScale = scaleBand()
      .domain(data.map(xValue))
      .range([0, innerWidth])
      .padding(0.2)

    const bandwidth =  xScale.bandwidth()
    const yScale = scaleLinear()
      .domain([0, max(data, yValue)])
      .range([innerHeight, 0])
      .nice();

    const xAxis = axisBottom().scale(xScale).tickSize(0);

    const yAxis = axisLeft().scale(yScale).tickSizeInner(-innerWidth).tickSizeOuter(0).tickFormat(format("d"));;
    const colorScale = scaleOrdinal(colorTheme);
    const tagBar = d => {
      const name = ["United Kingdom", "United States of America"].includes(d.region) ?d.abbrev : d.region
      if(name.length*Math.log(bandwidth)*5.6*0.7> innerHeight-yScale(yValue(d))){
        return true
      }
      else return false
    }
    const svg = select(ref.current);
    const chart = svg.select("g");

    const tooltip = select('.tooltip')
    .style('position', 'absolute')
    .style('z-index', '10')
    .style('visibility', 'hidden')
    .style('padding', '10px')
    .style('background', 'rgba(0,0,0,0.6)')
    .style('border-radius', '4px')
    .style('color', '#fff')
    .text('a simple tooltip');

    chart//background
      .selectAll(".background")
      .data([null])
      .join('rect')
      .classed('background',true)
      .attr('width',innerWidth)
      .attr('height',innerHeight)
      .style('fill',chartColour)

    const xAxisGroup = chart.selectAll(".xAxisGroup").data([null]).join("g").classed("xAxisGroup",true)
    const yAxisGroup = chart.selectAll(".yAxisGroup").data([null]).join("g").classed("yAxisGroup",true)


    xAxisGroup//x-axis
      .selectAll(".x-axis")
      .data([null])
      .join("g")
      .classed("x-axis", true)
      .style("font-size", "0em")
      .attr("transform", `translate(0,${innerHeight})`)
      .transition()
      .duration(duration)
      .call(xAxis)

    xAxisGroup//x-axis title
      .selectAll(".x-axis-title")
      .data([xAxisTitle])
      .join("text")
      .classed("x-axis-title", true)
      .attr("x", innerWidth / 2)
      .attr("y", innerHeight + xTitleOffset)
      .style("text-anchor", "middle")
      .style("font-size", axisSize)
      .text((d) => d);


    yAxisGroup//y-axis
      .selectAll(".y-axis")
      .data([null])
      .join((enter) =>
        enter
          .append("g")
          .style("font-size", "0.9em")
          .classed("y-axis", true)
          .attr("transform", "translate(0,0)")
        ,
        (update) =>
          update,
          (exit) => 
          exit
          .transition()
          .duration(duration)
          .remove()
      )
      .transition()
      .duration(duration)
      .call(yAxis)

    yAxisGroup//y-axis
      .selectAll(".xydomain")
      .data([null])
      .join((enter) =>
        enter
        .append("path")
          .classed("xydomain", true),
        (update)=>
          update
          ,
          (exit) => 
          exit
          .transition()
          .duration(duration)
          .remove()
      )      
      .transition()
      .duration(duration)
      .attr('fill','none')
      .attr('d',`M0,0V${innerHeight} H${innerWidth}`)
      .attr('stroke',"black")

    yAxisGroup//y-axis title
      .selectAll(".y-axis-title")
      .data([yAxisTitle])
      .join("text")
      .classed("y-axis-title", true)
      .attr("x", 0)
      .attr("y", yTitleOffset)
      .style("font-size", axisSize)
      .attr("transform", `translate(${yTitleOffset}, ${innerHeight / 2}) rotate(-90)`)
      //.attr("fill", "#000")
      .style("text-anchor", "middle")
      .text((d) => d);

    chart //chart title
        .selectAll(".chart-title")
        .data([Title])
        .join("text")
        .classed("chart-title",true)
        .attr("x", innerWidth / 2)
        .attr('y',titleOffset)
        .attr("fill", "#000")
        .style("font-size", "14px")
        .style("text-anchor", "middle")
        .text((d) => d);

    yAxisGroup.select('.tick').select('line').attr('opacity','0.1')

    
    chart//bars
      .selectAll(".bar")
      .data(data)
      .join((enter) =>
        enter
          .append("rect")
          .classed("bar", true)
          .attr("y", (d) => yScale(0))
          .attr("height", 0),
          (update) =>
          update,
        (exit) =>{
        exit
        .transition()
        .duration(duration)
        .delay((d, i) => ((length - i) * duration) / length)
        .attr("height", 0)
        .attr("y", innerHeight)
        .remove()}
      )
      .attr("x", (d) => xScale(xValue(d)))
      .style("fill", (d, i) => colorScale(i))
      .attr("width", (d) => bandwidth)
      .on('mouseover',  (d, i) => {
        const temp = d.target.__data__
        tooltip
          .html(
            `<div> ${country(temp)}:</div><div>${toolTipText + yValue(temp).toFixed(2)}  ${formatNumber}</div>`
          )
          .style('visibility', 'visible');
        select(this).transition().attr('fill', "#eec42d");
        })
        .on('mousemove', function (d) {
            tooltip
            .style('top', d.pageY -110 + 'px')
            .style('left', d.pageX +30+ 'px');
        })
        .on('mouseout', function () {
            tooltip.html(``).style('visibility', 'hidden');
            select(this).transition().attr('fill', '#437c90');
        })
      .transition()
      .duration(duration)
      .delay((d, i) => (i * duration) / length)
      .attr("height", (d) => innerHeight - yScale(yValue(d)))
      .attr("y", (d) => yScale(yValue(d)));
      

    
    chart
      .selectAll(".tags")
      .data(data)
      .join((enter) =>
      enter
        .append("text")
          .classed("tags",true)
          .attr("transform",`rotate(-90)`)
          .attr('opacity',0)
          .attr("x",-innerHeight),
          (update) =>
          update,
        (exit) =>{
        exit
        .remove()}
    )
    .attr("font-size",d => `${Math.log(bandwidth)*5.6}px`)
    .attr("y", (d) => xScale(xValue(d))+bandwidth/2+(Math.log(bandwidth)*4.60)/2.4)
    .text(d => ["United Kingdom", "United States of America"].includes(d.region) ?d.abbrev : d.region)
    .style("text-anchor","left")    
    .attr('id',d => !tagBar(d)?"whitetag": "blacktag")
    .attr('fill', d => tagBar(d) ?'black':'white')
    .transition()
    .duration(duration)
    .delay((d, i) => (i * duration) / length)
    .attr('opacity',1)
    .attr("x", d => 12-innerHeight + (tagBar(d) ?innerHeight-11-yScale(yValue(d)):0))
  
  }
  return (
    <div id="barchart">
        <svg className="chart" width={width} height={height}  ref={ref}>
            <g transform={`translate(${margin.left}, ${margin.top})`}/>
        </svg>
        <div className="tooltip"/>
    </div>
  );
}