import {json} from 'd3'
import { useEffect, useState } from 'react';
const jsonURL = "https://gist.githubusercontent.com/anujan-k19/104a77c464deeff471758951ca800265/raw/dae2ed0d1625764cd005d7cdfad933baab256219/contentData.json"
export const LoadData = () => {

const [data, setData] = useState(null)
    useEffect(() => {
        const row = d => {
            d.Annual_GHI_kWh_m2= +d.Annual_GHI_kWh_m2
            d.CIEG2013_gCO2e_kWh= +d.CIEG2013_gCO2e_kWh
            d.Electricity_cost_USD_kWh= +d.Electricity_cost_USD_kWh
            d.ICEV_usage_emissions_gCO2e_km= +d.ICEV_usage_emissions_gCO2e_km
            d.Share_of_diesel_gasoline_vehicles= +d.Share_of_diesel_gasoline_vehicles
            d.VKTstat = +d.VKTstat
            return d;
        };
    json(jsonURL, row).then( data => {setData(data);
    })}, [])
    return data
}
