
import { 
  Flex,
  Heading,
  Center 
} from "@chakra-ui/react" 



import {VKTSelector, Sliders2, CaseSelector} from './SliderChak'
import {MapCreator} from '../Map/MapCreator'
import { InfoCalc } from "./infoCalc"
export const UIMaker = ({
  onClicked,
  setOnClicked,
  caseOption,
  setCaseOption,
  VKToption,
  setVKTOption,
  PV_performanceRatio,
  setPV_performanceRatio,
  PV_CellNominalEffiency,
  setPV_CellNominalEffiency,
  cellArea_ratio,
  setCellArea_ratio,
  modalHeight,
  setlength,
  graphWidth,
}) => {

  const caseArray = ['High Case','Medium Case','Low Case'];
  const energy_consumption_Wh_km  = [280,200,150]
  const vehicle_footprint_m2Array = [4.3,4.3,3.68]
  const PV_cellarea_cm2 = 153.3;
  const STCirradiance_W_m2 = 1000;
  const evCaseSelect = caseArray.indexOf(caseOption[0])
  let EV_Eff_Wh_km;
  let vehicle_footprint_m2;
  if (caseOption[1] === false) {
    EV_Eff_Wh_km = energy_consumption_Wh_km[evCaseSelect]
    vehicle_footprint_m2 =  vehicle_footprint_m2Array[evCaseSelect]
   }
   else {
   EV_Eff_Wh_km = caseOption[2]
    vehicle_footprint_m2 =  caseOption[3]
   }
 const PV_totalCellArea = vehicle_footprint_m2*cellArea_ratio/100
 const cellNumber = Math.floor(PV_totalCellArea*10000/PV_cellarea_cm2)
 const PV_CellNominalPower = PV_CellNominalEffiency/100*STCirradiance_W_m2*PV_cellarea_cm2/10000;
 const PV_arrayNominalPower = PV_CellNominalPower * cellNumber;
return (
  <Flex 
    className="UI-box"
    borderRadius="none"
    borderWidth="1px"
    borderColor="grey.100"
    direction="column" 
    width="100%"
    alignItems="center" 
    justifyContent="center"
    bg="#fbfbfb" 
    padding = "0%" 
  >
    <Center width="100%" height="70px"bg="#003E74" color="white"> 
    <Heading fontSize="14pt"> Choose your Options: </Heading>
    </Center>
    <MapCreator
        setlength = {setlength}
        setOnClicked = {setOnClicked}
        modalHeight  = {modalHeight}
        onClicked = {onClicked}
        tooltip = {"Here you can select which countries to look at on the map. Click the select button to open it up."}
    />        

    <CaseSelector
        label = {"Select EV Type:"}
        options={caseArray}
        selectedValue={caseOption}
        onSelectedValueChange={setCaseOption}
        tooltip = {"Here you can select the vehicle footprint and efficency either by low-high or custom"}
    />
    
    <VKTSelector 
      label={'VKT per year:'}
      min={12000} 
      max={21000} 
      selectedValue = {VKToption}
      onSelectedValueChange = {setVKTOption}
      tooltip = {"Here you can select the vehicle kilometres travelled: either specific for each region or a universal custom average"}
      />
    <Sliders2 
      label={'Performance Ratio:'}
      min={40} 
      max={95}
      selectedValue = {PV_performanceRatio}
      onSelectedValueChange = {setPV_performanceRatio}
      tooltip = {"This sets the cell performance ratio: the ratio between the theoretical and actual output of the cell."}
    />
    <Sliders2 
      label={'PV Cell Efficiency:'}
      min={15} 
      max={35} 
      selectedValue = {PV_CellNominalEffiency}
      onSelectedValueChange = {setPV_CellNominalEffiency}
      tooltip = {"Select how efficient one photovoltaic cell is, i.e. energy losses."}
    />
    <Sliders2 
      label={'Cell-Area Ratio:'}
      min={70} 
      max={95} 
      selectedValue = {cellArea_ratio}
      onSelectedValueChange = {setCellArea_ratio}
      tooltip = {"Choose how much of the car's footprint is covered by PV cells."}
    />
    <InfoCalc
        totalCellArea = {PV_totalCellArea}
        nominalPower = {PV_arrayNominalPower}
        vehicle_footprint_m2 = {vehicle_footprint_m2}
        graphWidth = {graphWidth}
    />
  </Flex>
)}