
import { Linescatter } from "../D3_Charts/linescatterChart";
import { Linechart } from "../D3_Charts/lineChart";

import { Flex,Text } from "@chakra-ui/layout";
import {schemeAccent, schemeCategory10,} from 'd3'
import { useState } from "react";

export const GraphSelector = ({
    dataOriginal,
    graph,
    graphWidth,
    attributes,
    }) => {
        const height = 450;
        const [legHov, onLegHov] = useState(null)
        const [legClick, onLegClick] = useState(null)
        const axisSize = "1em";
        const chartColour = 'rgba(235, 238, 238, 0.5)';
        const yTitleOffset = -30;
        const xTitleOffset = 50;
        const titleOffset = 20;
        const margin = {
            top: 30,
            bottom: 80,
            left: 80,
            right: 30,
          };

    if (graph === attributes[0]) {
        return <Linescatter
        graphWidth={graphWidth}
        height = {height}
        dataOriginal={dataOriginal}
        xAxisTitle = {'PV on EV Emissions [gCO2e/kWh]'}
        yAxisTitle ={'EV Emissions [gCO2e/kWh]'}
        Title = {''}
        xValue = {d => d.PVonEV_usageEmissions_Years}
        yValue  = {d => d.EV_Usage_Emissions_Years}
        xTitleOffset = {xTitleOffset}
        yTitleOffset = {yTitleOffset}
        titleOffset = {titleOffset}
        chartColour = {chartColour}
        axisSize = {axisSize}
        margin = {margin}
        colorTheme={schemeCategory10}
        radius = {5}
        country = {d => d.region}
        toolTipText1 = "x: "
        toolTipText2 = "y: "
        formatNumber1 = ""
        formatNumber2 = ""
        abbrev = {d => d.abbrev}
        legHov = {legHov}
        onLegHov = {onLegHov}
        legClick = {legClick}
        onLegClick = {onLegClick}
        />
        }
    else if (graph === attributes[1]) {
        return <Linechart
        graphWidth={graphWidth}
        height = {height}
        dataOriginal={dataOriginal}
        xAxisTitle = {'Year'}
        yAxisTitle ={'EV Emissions [gCO2e/kWh]'}
        Title = {''}
        xValue = {d => d.Year}
        yValue  = {d => d.EV_Usage_Emissions_Years}
        xTitleOffset = {xTitleOffset}
        yTitleOffset = {yTitleOffset}
        titleOffset = {titleOffset}
        chartColour = {chartColour}
        axisSize = {axisSize}
        margin = {margin}
        colorTheme={schemeCategory10}
        radius = {5}
        country = {d => d.region}
        toolTipText1 = "Year: "
        toolTipText2 = "Value: "
        formatNumber1 = ""
        formatNumber2 = ""
        abbrev = {d => d.abbrev}
        legHov = {legHov}
        onLegHov = {onLegHov}
        legClick = {legClick}
        onLegClick = {onLegClick}
        />
        }
    else if (graph === attributes[2]) {
        return <Linechart
        graphWidth={graphWidth}
        height = {height}
        dataOriginal={dataOriginal}
        xAxisTitle = {'Year'}
        yAxisTitle ={'PV on EV Emissions [gCO2e/kWh]'}
        Title = {''}
        xValue = {d => d.Year}
        yValue  = {d => d.PVonEV_usageEmissions_Years}
        xTitleOffset = {xTitleOffset}
        yTitleOffset = {yTitleOffset}
        titleOffset = {titleOffset}
        chartColour = {chartColour}
        axisSize = {axisSize}
        margin = {margin}
        colorTheme={schemeCategory10}
        radius = {5}
        country = {d => d.region}
        toolTipText1 = "Year: "
        toolTipText2 = "Value: "
        formatNumber1 = ""
        formatNumber2 = ""
        abbrev = {d => d.abbrev}
        legHov = {legHov}
        onLegHov = {onLegHov}
        legClick = {legClick}
        onLegClick = {onLegClick}
        />
        }
    else if (graph === attributes[3]) {
            return <Linechart
            graphWidth={graphWidth}
            height = {height}
            dataOriginal={dataOriginal}
            xAxisTitle = {'Year'}
            yAxisTitle ={'CIEG [gCO2e/kWh]'}
            Title = {''}
            xValue = {d => d.Year}
            yValue  = {d => d.CIEG_gCO2_kWh_Years}
            xTitleOffset = {xTitleOffset}
            yTitleOffset = {yTitleOffset}
            titleOffset = {titleOffset}
            chartColour = {chartColour}
            axisSize = {axisSize}
            margin = {margin}
            colorTheme={schemeCategory10}
            radius = {5}
            country = {d => d.region}
            toolTipText1 = "Year: "
            toolTipText2 = "Value: "
            formatNumber1 = ""
            formatNumber2 = ""
            abbrev = {d => d.abbrev}
            legHov = {legHov}
            onLegHov = {onLegHov}
            legClick = {legClick}
            onLegClick = {onLegClick}
            />
            }
    else {
        return <Flex alignItems="center" width={document.body.clientWidth/2 }><Text>invalid</Text></Flex>
    }
}
    