
export const Line = ({
    data, 
    xScale, 
    yScale, }) =>{
return (
data.map(d => (
            <line 
            opacity={0.5}
            stroke="black"
            x1={xScale(0)}
            y1={yScale(0)}
            x2={xScale(5.52)}
            y2={yScale(d.Wp)}
            />
        )
    )
)
}