import {
    select,
    scaleLinear,
    axisBottom,
    axisLeft,
    max,
    scaleOrdinal,

} from "d3";


import React, { useRef, useEffect } from "react";

export const Scatter = ({ 
    graphWidth,
    dataOriginal,  
    onClickValue,
    xAxisTitle,
    yAxisTitle,
    Title,
    xValue,
    yValue,
    xTitleOffset,
    yTitleOffset,
    titleOffset,
    colorTheme,
    radius,
    country,
    toolTipText1,
    formatNumber1,
    toolTipText2,
    formatNumber2,
    height,
    dataLength,
    abbrev,
    labelOn,
    setLabel,
    chartColour,
    axisSize,
    margin,
}) => {
  const duration = 1000;
  const width= graphWidth; 
  const data = dataOriginal.filter(d => onClickValue.includes(country(d)))
  const innerWidth = width - margin.left - margin.right;
  const innerHeight = height - margin.top - margin.bottom;
  const ref = useRef();

  let length;
  if (data.length > 0) 
    length = data.length
  else length = dataLength


  useEffect(() => {
    draw();
  }, [data]); 

  const draw = () => {
    
    const xScale = scaleLinear()
      .domain([0, max(data, xValue)])
      .range([0,innerWidth])
      .nice();

    const yScale = scaleLinear()
      .domain([0, max(data, yValue)])
      .range([innerHeight, 0])
      .nice();

    const xAxis = axisBottom().scale(xScale).tickSizeInner(-innerHeight).tickPadding(10);

    const yAxis = axisLeft().scale(yScale).tickSizeInner(-innerWidth).tickPadding(10);

    const colorScale = scaleOrdinal(colorTheme);

    const svg = select(ref.current);
    const chart = svg.select("g");

    const tooltip = select('.tooltip')
        .style('position', 'absolute')
        .style('z-index', '10')
        .style('visibility', 'hidden')
        .style('padding', '10px')
        .style('background', 'rgba(0,0,0,0.6)')
        .style('border-radius', '4px')
        .style('color', '#fff')
        .text('a simple tooltip');

    chart//background
      .selectAll(".background")
      .data([null])
      .join('rect')
      .classed('background',true)
      .attr('width',innerWidth)
      .attr('height',innerHeight)
      .style('fill',chartColour)

    const xAxisGroup = chart.selectAll(".xAxisGroup").data([null]).join("g").classed("xAxisGroup",true)
    const yAxisGroup = chart.selectAll(".yAxisGroup").data([null]).join("g").classed("yAxisGroup",true)
  
    xAxisGroup//y-axis
      .selectAll(".x-axis")
      .data([null])
      .join((enter) =>
        enter
          .append("g")
          .style("font-size", "0.9em")
          .classed("x-axis", true)
          .attr("transform", `translate(0,${innerHeight})`),
        (update) =>
          update,
          (exit) => 
          exit
          .transition()
          .duration(duration)
          .remove()
      )
      .transition()
      .duration(duration)
      .call(xAxis);

    

    xAxisGroup//x-axis title
      .selectAll(".x-axis-title")
      .data([xAxisTitle])
      .join("text")
      .classed("x-axis-title", true)
      .attr("x", innerWidth / 2)
      .attr("y", innerHeight + xTitleOffset)
      .style("font-size", axisSize)
      .attr("fill", "black")
      .style("text-anchor", "middle")
      .text((d) => d);

    
    yAxisGroup//y-axis
    .selectAll(".y-axis")
    .data([null])
    .join((enter) =>
      enter
        .append("g")
        .style("font-size", "0.9em")
        .classed("y-axis", true)
        .attr("transform", "translate(0,0)"),
      (update) =>
        update,
        (exit) => 
        exit
        .transition()
        .duration(duration)
        .remove()
    )
    .transition()
    .duration(duration)
    .call(yAxis);
    
    yAxisGroup//y-axis title
      .selectAll(".y-axis-title")
      .data([yAxisTitle])
      .join("text")
      .classed("y-axis-title", true)
      .attr("x", 0)
      .attr("y", yTitleOffset)
      .attr("opacity",1)
      .style("font-size", axisSize)
      .attr("transform", `translate(${yTitleOffset}, ${innerHeight / 2}) rotate(-90)`)
      .attr("fill", "#000")
      .style("text-anchor", "middle")
      .text((d) => d);
      
  yAxisGroup//y-axis
      .selectAll(".xydomain")
      .data([null])
      .join((enter) =>
        enter
          .append("path")
          .classed("xydomain", true),
        (update)=>
          update
          ,
          (exit) => 
          exit
          .transition()
          .duration(duration)
          .remove()
      )      
      .transition()
      .duration(duration)
      .attr('fill','none')
      .attr('d',`M0,0V${innerHeight} H${innerWidth}`)
      .attr('stroke',"black")

    chart //chart title
        .selectAll(".chart-title")
        .data([Title])
        .join("text")
        .classed("chart-title",true)
        .attr("x", innerWidth / 2)
        .attr('y',titleOffset)
        .attr("fill", "#000")
        .style("font-size", "14px")
        .style("text-anchor", "middle")
        .text((d) => d);

    
    chart//points
      .selectAll(".textmark")
      .data(data)
      .join((enter) =>
        enter
        .append('text')
        .attr("y", d => yScale(yValue(d)) - 10)
        .attr("x", d => xScale(xValue(d)))
        .classed("textmark",true)
        .attr('opacity',0)
        .style("text-anchor", "middle"),
      (update) =>
          update,
      (exit) => 
        exit
          .transition()
          .duration(duration)
          .delay((d, i) => ((length - i) * duration) / length)
          .attr('opacity',0)
          .remove())
      .transition()
      .duration(duration)
      .delay((d, i) => (i * duration) / length)
      .text(d => abbrev(d))
      .attr('opacity',labelOn ?1 :0)
      .attr("y", d => yScale(yValue(d)) - 10)
      .attr("x", d => xScale(xValue(d)))


    chart//points
      .selectAll('.mark')
      .data(data)
      .join((enter) =>
        enter
          .append("circle")
          .classed("mark", true)
          .attr("cy", d => yScale(0))
          .attr("cx", d => xScale(0))
          .attr('r',0),
        (update) =>
          update,
          (exit) => 
          exit
          .transition()
          .duration(duration)
          .delay((d, i) => ((length - i) * duration) / length)
          .attr('r',0)
          .remove()
      )
      .style("fill", (d, i) => colorScale(i))
      .on('mouseover',  (d, i) => {
        const temp = d.target.__data__
        tooltip
          .html(
            `<div>${country(temp)}:</div><div>${toolTipText1 + xValue(temp).toFixed(2)} ${formatNumber1}</div><div>${toolTipText2 + yValue(temp).toFixed(2)} ${formatNumber2}</div>`
          )
          .style('visibility', 'visible');
        select(this).transition().attr('fill', "#eec42d");
        })
        .on('mousemove', function (d) {
            tooltip
            .style('top', d.pageY -10 + 'px')
            .style('left', d.pageX +10 + 'px');
        })
        .on('mouseout', function () {
            tooltip.html(``).style('visibility', 'hidden');
            select(this).transition().attr('fill', '#437c90');
        })
      .transition()
      .duration(duration)
      .delay((d, i) => (i * duration) / length)
      .attr("cy", (d) => yScale(yValue(d)))
      .attr("cx", (d) => xScale(xValue(d)))
      .attr('r', radius)

    const labelsetter = chart
      .selectAll('.labelset')
      .data([null])
      .join((enter) => {
        let g = enter.append("g")
        .classed("labelset", true)
        .attr('transform',`translate(20,40)`)
        .style('cursor','pointer')

        g.append('rect')
        .attr('width',120)
        .attr('rx', 5)
        .attr('height',30)
        .attr('y',-20)
        //.style('stroke',"black")
        .attr('opacity','0.65')

        g.append('text')
        .attr('x',20)
        .style('text-align',"centre")
        return g},
      (update) => 
      update,
      (exit) =>{
      exit
      .transition()
      .duration(duration)
      .remove()}
      )
      .on('click', () => {
      if (labelOn){
        setLabel(false)}
      else setLabel(true)
      })

    labelsetter.selectAll('text')
      .text(labelOn?'Labels On':'Labels Off')
      .style('fill', labelOn? 'black' : 'white')
    labelsetter.select('rect')
      .style('fill', labelOn? 'white' : 'black')
      
  };

  return (
    <div id="barchart">
        <svg className="chart" width={width} height={height}  ref={ref}>
            <g transform={`translate(${margin.left}, ${margin.top})`}/>
        </svg>
        <div className="tooltip"/>
    </div>
  );
}