import { Flex,Box,Heading,Text,Icon,Tooltip } from "@chakra-ui/react"
import {AiOutlineInfoCircle} from "react-icons/ai"
import {Linechart} from './Np_vs_Area/lineReact'
import { useState } from "react"
import {FaCrosshairs} from 'react-icons/fa'
export const StudyCaseComp = ({
  graphWidth,
}) =>{
  const [casePosition, setCasePosition] = useState({x: 300/graphWidth, y: 210})
  const [studyCase, setStudyCase] = useState([3.37645968489342,772.8000000000001])
  return (
    <Flex className="row" height="500px"  marginInline="10px">
        <Flex className="column" width="40%">
          <Flex 
            borderWidth="0px" 
            direction="column" 
            width="100%"
            height="90%" 
            alignItems="left" 
            justifyContent="center"
            background="#fffff"
            padding="5%"
            >
            <Heading className="sectionHeading" borderTop="none" fontSize="1.4rem">Visualisation of Current EVs </Heading>
            <Text mline={'5%'} marginBlock="2%">There are currently multiple PV-on-EV vehicles on the market currently, but it is difficult to compare them to what is required to meet future goals.
             The graph on the right is plotted with a few different car models shown by the teals points, you can hover over them to see the details. You can also click anywhere on the chart and create a custom
             specification for a car, represented by <Icon as={FaCrosshairs} fill = {"#2B6CB0"} paddingBottom="1px" fontSize={20} text-align = {"centre"}/>.</Text>
            <Text mline={'5%'} marginBlock="2%">The lines represent the PV-cell efficiency required to achieve these stats, with the currently feasible ones shown.</Text>
            <br/>
            <Heading background="white"//"#003e74" 
            color="white" height="50px" paddingTop="10px" borderTopRadius="none" fontWeight="bold" marginInline={"auto"} fontSize="1.1rem" width="300px"  textAlign="center"> Custom Case:</Heading>
            <Flex 
              background="rgba(0, 62, 116,0.9)"
              color="white"
              alignSelf="center" 
              fontWeight="bold" 
              width="100%" 
              height="30px" 
              paddingBlock="30px" 
              borderBottomWidth="1px"
              borderBottomRadius="none"
              borderLeftWidth=  "1px"
              borderLeftColor = "grey.100"
              borderRightWidth=  "1px"
              borderRightColor = "grey.100"
              borderBottomColor = "grey.100"//"#003e74"
              display="flex" 
              direction="row" 
              justifyContent="center" 
              alignItems="center">
            <Text paddingInline="20px">{`PV Nominal Power: ${studyCase[1].toFixed(2)} Wp`}  </Text> 
            <Text paddingInline="20px">{` Total Cell-Area: ${studyCase[0].toFixed(2)} m²`}</Text>
            <Tooltip hasArrow  textAlign="top" padding="10px" borderRadius='7px' label={"Nominal power refers to the power output of the EV with PV, and the cell-area refers to the area of the car covered by PV cells. These variables are used are calculated from the variables above."}>
            <span>
            <Icon as={AiOutlineInfoCircle} paddingBottom="1px" marginInline="5px" _hover={{opacity:0.3}}/> 
            </span>
            </Tooltip>
            </Flex>
          </Flex>
        </Flex>
      <Flex className="column" width="60%">
        <Flex 
          borderWidth="0px" 
          direction="column" 
          width="100%"
          height="90%" 
          alignItems="center" 
          justifyContent="center"
          background="white"
          >
            <Box flex="1" height="100%" >
              <Linechart
                studyCase={studyCase}
                setStudyCase={setStudyCase}
                graphWidth = {graphWidth}
                setCasePosition={setCasePosition}
                casePosition={casePosition}
              />
            </Box>
          </Flex>
        </Flex>
      </Flex>
)}