
export const LineTags = ({
    data, 
    xScale, 
    yScale, 
}) =>{
return (
data.map(d => (
            <text
                x={xScale(5.52)+5}
                y={yScale(d.Wp)+5}
                opacity={0.5}
                textAnchor="center"
            >{d.n_cell}</text>
        )
    )
)
}