import { 
    Tooltip,
} from "@chakra-ui/react"

import {AiFillCar }from 'react-icons/ai'
export const Points = ({cars, xScale, yScale, studyCase, margin, offset}) => {
    
    return cars.map(d => (
    <g
        transform={`translate(${xScale(d.Equiv_PV_cell_area_m2)},${yScale(d.PV_nominal_power_Wp)})`} 
    >
    <Tooltip hasArrow textAlign="center" label={d.Automaker_Model} closeDelay={1000}>
    <circle 
        key={d.Automaker_Model}
        className = "point"
        size={15}
        r = {5}
        fill = {"#38B2AC"}
        
    />
    </Tooltip>

    </g>
    ))
}
