import React, { useState, useEffect} from 'react';
import {
  ChakraProvider,
   Heading,
    Flex,
    Text,
    Box,
    UnorderedList,
    ListItem,
} from "@chakra-ui/react"
import textjson from "../textfile.json"
import { LoadData } from "./Calculations/loadData.js";
import './app.css'
import { StudyCaseComp } from './Components/StudyCase/studyCase.js';
import { Navbar } from './Components/navbar.js';
import { MainModel } from './Components/MainModel/MainModel.js';
import { SecondModel } from './Components/SecondModel/SecondModel.js';
import theme from './impTheme'
import { Intro } from './Components/Intro.js';
const App = () => {

  const [graphWidth,setGraphWidth] = useState(document.body.clientWidth/2.5)
  const [modalHeight, setModalHeight] = useState(window.innerHeight*0.8)
  useEffect(() => {
    const setting = () => {
      setGraphWidth(document.body.clientWidth/2.2)
      setModalHeight(window.innerHeight*0.8)
    }
    window.addEventListener('resize', setting);
    return () => {
    window.removeEventListener('resize', setting);
    }
  }, [])

  const dataRaw = LoadData()
  if (!dataRaw){
    return <pre></pre>
  }

  
  return (
    <>
  <ChakraProvider theme={theme} color="#EBEEEE">
    <Navbar sections={["World Model","More Models","More Information"]}/>
    <Box id="content">

    <Flex id="Home" className="section" >
    <Flex className="innerSection">
      <Flex className="row" alignItems="center" background="white">
      <Heading className="sectionHeading" > Introduction</Heading>
      </Flex>
      <Intro text={textjson[0]}/>
      </Flex>
    </Flex>

    <Flex id="World Model" className="section">
    <Flex className="innerSection">
      <Flex className="row" alignItems="center">
        <Heading className="sectionHeading">World Model</Heading>
      </Flex>
      <MainModel
          graphWidth = {graphWidth}
          dataRaw = {dataRaw}
          modalHeight = {modalHeight}
          text={textjson[1]}
      />
    </Flex>
    </Flex>
    <Flex id="More Models" className="section" >
      <Flex className="innerSection">
        <Flex className="row" alignItems="center">
          <Heading className="sectionHeading">More Models</Heading>
        </Flex>
          <SecondModel 
            graphWidth = {graphWidth}
            dataRaw = {dataRaw}
            text={textjson[2]}
          />
      </Flex>
      <Flex className="innerSection">
          <StudyCaseComp
            graphWidth = {graphWidth}
          />
      </Flex>
    </Flex>
    </Box>
    <Flex id="More Information" className="footer" height="auto">
      <Flex className="column">
        <Flex className="row">
        <Heading borderColor="white" color="white" className="sectionHeading" background="none"> More Information</Heading>
        </Flex>
        <Flex color="white">
          <Text marginInline="5%" marginBlock={"10px"} width={graphWidth}>
          {textjson[3].p1}
          </Text>
          <UnorderedList marginInline="5%" marginBlock={"10px"}>
            <ListItem ><Text>{textjson[3].l1}</Text></ListItem>
            <ListItem marginBlock="10px"><Text>{textjson[3].l2}</Text></ListItem>
            <ListItem marginBlock="10px"><Text>{textjson[3].l3}</Text></ListItem>
          </UnorderedList>
        </Flex>
      </Flex>
    </Flex>
    </ChakraProvider>
    </>
    );
};


export default App




