import {useData} from './mapData'
import {Dropdown} from '../UI/DropdownChak'

import { useState } from 'react';
import {
  Flex, 
  Center,
  Button,
  Modal,
  ModalOverlay,
  ModalContent,
  useDisclosure,
} from  "@chakra-ui/react"

import { ModalSelector } from './ModalSelector';

export const MapCreator = ({
    setlength,
    onClicked,
    setOnClicked,
    modalHeight,
  tooltip,
}) => {

    const data = useData();

    const { isOpen, onOpen, onClose } = useDisclosure()
    const [mapClicked, setMapClicked] = useState(false)

    const [countries, setCountries] = useState(onClicked)

    const [mapSelect, onMapSelect] = useState("World")
    const attributes = [
        {type: "World", title:"Country"},
        {type: "United States", title:"State"},
        {type: "Europe", title:"Country"}
      ] ;

    return (
    <>
    <Flex className="buttonSpace" width='90%'>
    <Flex className="column" width = "40%"  justifyContent="center">
    <Dropdown
      label = {"Select Map:"}
      options={attributes.reduce((array, obj) => (array.push(obj.type), array), [])}
      id = {'menuDrop'}
      selectedValue={mapSelect}
      onSelectedValueChange={onMapSelect}
      width={"90%"}
      tooltip = {tooltip}
    />
    </Flex>
    <Center>
    <Button 
      width = "100%"
      //colorScheme="purple"
      bg = "purple.500"
      color = "white"
      _hover={{ bg: "purple.700" }}
      _focus={{ boxShadow: "outline" }}
      onClick={() => {
        onOpen()
        setMapClicked(false)
    }}>Select {attributes[attributes.reduce((array, obj) => (array.push(obj.type), array), []).indexOf(mapSelect)].title}</Button>
    </Center>
    </Flex>

    <Modal isOpen={isOpen} onClose={onClose} >
        <ModalOverlay/>
        <ModalContent maxW={document.body.clientWidth/2} height={modalHeight}>
          <ModalSelector
            setlength = {setlength}
            onClicked = {onClicked}
            setOnClicked = {setOnClicked}
            data = {data}
            onClose = {onClose}
            mapClicked = {mapClicked}
            setMapClicked= {setMapClicked}
            countries = {countries}
            setCountries = {setCountries}
            mapSelect = {mapSelect}
            height = {modalHeight}
            width = {document.body.clientWidth/2}
          />
        </ModalContent>
    </Modal>
    </>)}