import React from 'react';
import {Select,Flex,Center,Tooltip,Icon} from "@chakra-ui/react" 
import { AiOutlineInfoCircle } from 'react-icons/ai';
export const Dropdown = ({ 
  options, 
  id, 
  selectedValue, 
  onSelectedValueChange, 
  label,
  width,
  tooltip,
  }) => (
<Flex width={width}>
<Center w="100%">
    <label width="25%" display="flex" flex-direction="row"> {label}</label>
    <Center paddingTop="0px">
            <Tooltip hasArrow textAlign="top" label={tooltip} padding="10px" borderRadius='7px'>
            <Center w="20px" h="20px" verticalAlign="middle">
            <Icon as={AiOutlineInfoCircle} marginInline="5px" _hover={{opacity:0.3}}/> 
            </Center>
            </Tooltip>
            </Center>
  <Select bg="white"  mline="2" width={"60%"} id={id} defaultValue={selectedValue} onChange={event => onSelectedValueChange(event.target.value)}>
    
    {options.map( d => (

      <option value={d} key={d}>
        {d}
      </option>

    ))}

  </Select>
  </Center>
</Flex>
);