import {
    ListItem,
    List,
    Flex,
    Link,
    Center,
    useDisclosure,
    IconButton,
    Box,
    Stack,
} from '@chakra-ui/react'
import { HamburgerIcon, CloseIcon } from '@chakra-ui/icons';
export const Navbar = ({
    sections
}) => {
    const { isOpen, onOpen, onClose } = useDisclosure();
    return(
    <>
    <Flex className="nav" height="50px" id="navshadow">        
    <Flex alignItems={'center'} justifyContent={'space-between'}>
        <IconButton
            height="40px"
            color="white"
            background="none"
            borderRadius="4px"
            className="navText"
            marginInline="5px"
            size={'lg'}
            icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
            aria-label={'Open Menu'}
            display={{ lg: 'none' }}
            onClick={isOpen ? onClose : onOpen}
        />
    </Flex>
        <List>      
                    <ListItem
                            textAlign={"center"}
                            float={{base: "none", lg:"left" }}
                            key={"home"}
                        >
                        <Center 
                        h="50px"
                        >
                        <Link
                            height="40px"
                            width="100%"
                            className="navText"
                            href ={"#Home"}
                            borderRadius="4px"
                            paddingBlock="5px"
                            paddingInline="50px"
                            fontSize="1.2rem"
                            fontWeight="bold"
                            marginInline="5px"
                            color="white"
                            backgroundColor="none"
                            display= "inline-block"
                            style={{ textDecoration: 'none' }}
                            >
                                {"Home"}
                            </Link>
                            </Center>
                        </ListItem>
            <List display={{ base: 'none', lg: 'contents' }}>
            {sections.map( d => (
                // <Flex float="left" height="100%">
                        <ListItem
                            float="left"
                            key={d}
                        >
                        <Center 
                        h="50px"
                        >
                        <Link
                            height="40px"
                            width="100%"
                            className="navText"
                            href ={"#"+d}
                            borderRadius="4px"
                            paddingBlock="5px"
                            paddingInline="50px"
                            fontSize="1.2rem"
                            fontWeight="bold"
                            marginInline="5px"
                            color="white"
                            backgroundColor="none"
                            display= "inline-block"
                            style={{ textDecoration: 'none' }}

                            >
                                {d}
                            </Link>
                            </Center>
                        </ListItem>
               
            ))} </List>
        </List>
        
    </Flex>
    {isOpen ? (
          <Box pb={4} display={{lg:"none"}} >
            <Stack background={"#003E74"} className="nav" marginTop={50} spacing={"5px"}>
            {sections.map( d => (
                    <Center 
                        h="50px"
                    >
                        <Link
                            height="40px"
                            width="100%"
                            className="navText"
                            href ={"#"+d}
                            borderRadius="4px"
                            paddingBlock="5px"
                            paddingInline="108px"
                            fontSize="1.2rem"
                            fontWeight="bold"
                            marginInline="5px"
                            color="white"
                            display= "inline-block"
                            style={{ textDecoration: 'none' }}
                            >
                                {d}
                            </Link>
                    </Center>
            ))}
            </Stack>
          </Box>
        ) : null}
        </>
    )
        }